import React, { useState, useCallback, useEffect } from 'react';
import { List, Checkbox, message } from 'antd';
import request from '../request';
import { RouteComponentProps } from 'react-router';

const A: React.FC<RouteComponentProps> = (props) => {

	const [data, setData] = useState<{ roleId: number, name: string, remark: string }[]>([])
	const [eData, setEData] = useState<{ roleId: number, name: string }[]>([])
	const [loginId] = useState(new URLSearchParams(props.location.search).get('loginId') || '')
	const [loading, setLoading] = useState(false)

	/**
	 * 更新列表数据
	 */
	const updateData = useCallback(() => {

		const search = {
			pageIndex: 1,
			pageSize: 100,
			roleType: 0
		}
		request.role.getRoleInfo(search).then(res => {

			if (res.status && res.data.status) {
				setData(res.data.data.rows)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [])

	const getER = useCallback(() => {
		request.emprole.getEmployeeRole({
			pageIndex: 1,
			pageSize: 100,
			loginId
		}).then(res => {
			if (res.status && res.data.status) {
				setEData(res.data.data.rows)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [loginId])

	const selectRole = (roleId: number, selected: boolean) => {
		if (selected) {
			setLoading(true)
			request.emprole.addRoleEmployee({ loginId, roleId }).then(res => {
				setTimeout(() => {
					setLoading(false)
				}, 500);

				if (res.status === 200 && res.data.status) {
					message.success('角色人员修改已保存！')
					getER()
				} else {
					message.error((res.data && res.data.errorMsg) || res.statusText)
				}
			})
		} else {

			setLoading(true)
			request.emprole.deleteRoleEmployee({ loginId, roleId }).then(res => {
				setTimeout(() => {
					setLoading(false)
				}, 500);

				if (res.status === 200 && res.data.status) {
					message.success('角色人员修改已保存！')
					getER()
				} else {
					message.error((res.data && res.data.errorMsg) || res.statusText)
				}
			})
		}
	}

	useEffect(() => {
		getER()
		updateData()
	}, [getER, updateData])

	return (
		<List

			style={{ padding: 20 }}
			itemLayout="horizontal"
			size='small'
			header={<div>权限设置</div>}
			dataSource={data.map((v) => ({ ...v, key: v.roleId }))}
			renderItem={item => (
				<List.Item>
					<List.Item.Meta
						avatar={<Checkbox disabled={loading} checked={eData.find(v => v.roleId === item.roleId) !== undefined} onChange={e => {
							selectRole(item.roleId, e.target.checked)
						}}></Checkbox>}
						title={item.name}
						description={item.remark} />
				</List.Item>
			)}
		/>
	)
}

//const styles: { [key: string]: React.CSSProperties } = {
//}

export default A;