import React from 'react';
import { Form, Button, Input, Switch } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { formItemLayout, tailFormItemLayout } from './paper';

interface Props extends FormComponentProps {
	submit: (data: any) => void
}

export const PaperForm: React.FC<Props> = (props) => {

	const handleSubmit = () => {
		props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
				props.submit({ ...values, onWork: values.onWork ? 1 : 0 })
				props.form.resetFields()
			}
		});
	}

	const getFieldDecorator = props.form.getFieldDecorator

	return (
		<Form {...formItemLayout} onSubmit={handleSubmit}>

			<Form.Item label="登录名">
				{getFieldDecorator('loginId', {
					rules: [{ required: true, type: 'string', message: '请填写登录名' }]
				})(<Input />)}
			</Form.Item>
			<Form.Item label="登录密码">
				{getFieldDecorator('password', {
					rules: [{ required: true, type: 'string', message: '请填写登录密码' }]
				})(<Input type="password" />)}
			</Form.Item>
			<Form.Item label="用户昵称">
				{getFieldDecorator('userName', {
					rules: [{ required: true, type: 'string', message: '请填写用户昵称' }]
				})(<Input />)}
			</Form.Item>

			<Form.Item label="职务">
				{getFieldDecorator('duty')(<Input />)}
			</Form.Item>
			<Form.Item label="电话">
				{getFieldDecorator('phone')(<Input />)}
			</Form.Item>
			<Form.Item label="邮箱">
				{getFieldDecorator('email')(<Input />)}
			</Form.Item>
			<Form.Item label="地址">
				{getFieldDecorator('ipaddress')(<Input />)}
			</Form.Item>
			<Form.Item label='是否在职'>
				{
					getFieldDecorator('onWork', {
						rules: [{ type: 'boolean' }],
						valuePropName: 'checked',
						initialValue: true
					})(<Switch></Switch>)
				}
			</Form.Item>

			<Form.Item {...tailFormItemLayout}>
				<Button type="primary" htmlType="submit" style={{ minWidth: '120px' }}>提交</Button>
			</Form.Item>
		</Form>
	)
}

export default Form.create<Props>()(PaperForm);