
import React, { createRef } from 'react';
import { message, Empty } from 'antd';
import Tools, { getSize, getType, ToolType, colorItems } from '../cmps/tools';

import Line from './shapes/line';
import Rect from './shapes/rect';
import Pen from './shapes/pen';
import Text from './shapes/text';
import Ellipse from './shapes/ellipse';
import { requestFullScreen } from '../funcs';
import request from '../request';
export const svgNamespace = 'http://www.w3.org/2000/svg';

export interface PainterInfo {
	width: string
	color: string,
	shape: ToolType,
	current?: SVGElement
	move: 'lt' | 'rb' | 'all'
	point1?: SVGCircleElement
	point2?: SVGCircleElement
	mouseDown: boolean
	onSelect?: () => void
	changed: boolean
}

export let painter: PainterInfo = {
	width: '2',
	color: 'red',
	shape: 'pen',
	move: 'all',
	mouseDown: false,
	changed: false
}

export const selectItem = (painter: PainterInfo) => {

	painter.point1 && painter.point1.setAttribute('fill', 'white')
	painter.point2 && painter.point2.setAttribute('fill', 'white')
	painter.point1 && painter.point1.setAttribute('stroke', painter.current!.getAttribute('stroke') || 'red')
	painter.point2 && painter.point2.setAttribute('stroke', painter.current!.getAttribute('stroke') || 'red')

	painter.onSelect && painter.onSelect()
}

export const unSelectItem = (painter: PainterInfo) => {

	painter.point1 && painter.point1.setAttribute('fill', 'none')
	painter.point2 && painter.point2.setAttribute('fill', 'none')
	painter.point1 && painter.point1.setAttribute('stroke', 'none')
	painter.point2 && painter.point2.setAttribute('stroke', 'none')
	painter.current = undefined;
	painter.point1 = undefined;
	painter.point2 = undefined;
}

/**
 * SVG 箭头颜色
 */
const OriginSvg = () => {
	return (colorItems.map(v => <defs key={v.image}>
		<marker id={"Triangle-" + v.image} viewBox="0 0 40 40" refX="0" refY="10"
			markerWidth="10" markerHeight="10" orient="auto">
			<path d="M 0 0 L 20 10 L 0 20 z" fill={v.image} />
		</marker>
	</defs>))
}

interface Props {
	currentTool: string,
	currentSize: string,
	currentColor: string,
	menuIndex: number
	loadedImage: boolean,
	historyOff: boolean
	historys: {
		userName: string,
		pageUrl: string,
		pageSvgInfo: string,
		disposeDateTime: number,
		pageVer: number
	}[]
	scale: number
	page: {
		src: string
	},
	size: {
		width: number,
		height: number,

		toolHeight: number,
		sclar: number
	},

	infos: {
		pageVer: number,
		paperId: number,
		pageId: number,
		pfold: string,
		pageDate: string,
		loginId: string,
		userName: string
		sReviewInfo: string,
	}
}

class A extends React.Component<{ updated: () => void }, Props> {

	state: Props = {
		menuIndex: -1,
		scale: 1,
		currentTool: 'image4',
		currentSize: 'image12',
		currentColor: 'red',
		historyOff: true,
		loadedImage: true,
		historys: [],
		size: { width: 0, height: 0, toolHeight: 0, sclar: 1 },

		page: { src: '' },
		infos: {
			loginId: 'ceshi',
			userName: 'ceshi',
			sReviewInfo: '',
			pageVer: 0,
			paperId: 0,
			pageId: 0,
			pfold: '',
			pageDate: '',
		}
	}

	mainRef = createRef<HTMLDivElement>()
	toolsRef = createRef<HTMLDivElement>()
	svgRef = createRef<SVGSVGElement>()
	ref = createRef<HTMLDivElement>()
	//算工具条高度
	updateSize = () => {

		const rt = this.mainRef.current && this.mainRef.current.getClientRects()
		const rtTool = this.toolsRef.current && this.toolsRef.current.getClientRects()

		if (rt && rtTool && rt.length && rtTool.length) {
			this.setState({
				size: {
					width: rt[0].width,
					height: rt[0].height,
					toolHeight: rtTool[0].height,
					//缩放比例
					sclar: rt[0].width / 500
				},
				loadedImage: true
			})
		}
	}

	onMouseDown = (e: React.MouseEvent<HTMLDivElement>) => {
		if (this.state.loadedImage === false) {
			return
		}
		painter.mouseDown = true

		if (painter.shape === 'line') {
			Line.newItem(painter, this.state.size.sclar, e, this.svgRef.current!);
		}
		if (painter.shape === 'rect') {
			Rect.newItem(painter, this.state.size.sclar, e, this.svgRef.current!);
		}
		if (painter.shape === 'ellipse') {
			Ellipse.newItem(painter, this.state.size.sclar, e, this.svgRef.current!);
		}
		if (painter.shape === 'direct') {
			Line.newItem(painter, this.state.size.sclar, e, this.svgRef.current!, true);
		}
		if (painter.shape === 'pen') {
			Pen.newItem(painter, this.state.size.sclar, e, this.svgRef.current!);
		}
		if (painter.shape === 'text') {
			Text.newItem(painter, this.state.scale, this.state.size.sclar, e, this.svgRef.current!);
		}
	}

	onMouseUp = (e: React.MouseEvent<HTMLDivElement>) => {
		if (this.state.loadedImage === false) {
			return
		}
		painter.mouseDown = false;
		unSelectItem(painter)
	}

	onMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
		if (this.state.loadedImage === false) {
			return
		}
		if (painter.mouseDown) {
			if (painter.shape === 'line') {
				Line.mouseMove(painter, this.state.size.sclar, e);
			}
			if (painter.shape === 'rect') {
				Rect.mouseMove(painter, this.state.size.sclar, e);
			}
			if (painter.shape === 'ellipse') {
				Ellipse.mouseMove(painter, this.state.size.sclar, e);
			}
			if (painter.shape === 'direct') {
				Line.mouseMove(painter, this.state.size.sclar, e);
			}
			if (painter.shape === 'pen') {
				Pen.mouseMove(painter, this.state.size.sclar, e);
			}
			if (painter.shape === 'text') {
				Text.mouseMove(painter, this.state.size.sclar, e);
			}
			if (painter.shape === 'hand' && this.mainRef.current) {
				
				this.mainRef.current.parentElement && this.mainRef.current.parentElement.scrollBy(-e.movementX, -e.movementY)
			}
		}

	}
	onMouseLeave = (e: React.MouseEvent<HTMLDivElement>) => {

		if (this.state.loadedImage === false) {
			return
		}
		painter.mouseDown = false
	}

	getPageReview = (info: any) => {

		painter.changed = false;
		this.setState({
			page: { src: '' },
			loadedImage: false,
			infos: { ...this.state.infos, ...info }
		})
		request.page.getPageImageUrl(info).then(res => {
			if (res.status && res.data.status && res.data.data.rows.length > 0) {

				this.setState({
					page: { src: res.data.data.rows[0].pageUrl },
					loadedImage: true,
					infos: { ...this.state.infos, ...info }
				})
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}

	componentDidMount() {
		this.updateSize()
		window.addEventListener('resize', this.updateSize)

		painter.onSelect = () => {

			if (painter.current) {
				painter.width = (painter.current.getAttribute('stroke-width') || painter.current.getAttribute('font-size') || '2').replace('em', '');
				painter.shape = (painter.current.getAttribute('type') || 'line') as ToolType
				const s = getSize(painter.width);
				const t = getType(painter.shape)

				if (this.state.currentSize !== s) {
					this.setState({ currentSize: s })
				}

				if (this.state.currentTool !== t) {
					this.setState({ currentTool: t })
				}

				const input = document.getElementById('svgmain_input');
				if (input) {
					input.style.visibility = 'hidden';
				}
			}
		}

		return () => {
			painter.onSelect = undefined
			window.removeEventListener('resize', this.updateSize)
		}
	}

	render() {
		return (
			<div style={{ height: '100%', overflow: 'hidden' }}>
				<div ref={this.ref} style={{ height: '100%', width: '100%', position: 'relative' }}>
					<Tools
						draw={true}
						disabled={!this.state.loadedImage} refM={this.toolsRef}
						currentColor={this.state.currentColor}
						currentSize={this.state.currentSize}
						currentTool={this.state.currentTool}
						save={() => {
							if (this.svgRef.current) {
								//删掉pt=true
								this.svgRef.current.querySelectorAll('[pt]').forEach(v => {
									v.parentElement!.removeChild(v)
								})

								request.page.savePageImageReview({
									...this.state.infos,
									sReviewInfo: this.svgRef.current.innerHTML
								}).then(res => {
									if (res.status && res.data.status) {
										painter.changed = false
										this.getPageReview(this.state.infos);
										message.success('审版信息已上传！');
										this.props.updated()
									} else {
										message.error((res.data && res.data.errorMsg) || res.statusText)
									}
								})
							}
						}}
						scale={v => {
							if (v) {
								this.setState({ scale: this.state.scale * 1.5 }, () => {
									this.updateSize()
								})
							} else {
								if (this.state.scale > 1) {
									this.setState({ scale: this.state.scale / 1.5 }, () => {
										this.updateSize()
									})
								}
							}
						}}
						setColor={v => {
							if (painter.current) {
								if (painter.shape === 'text') {
									painter.current.setAttribute('fill', v)
								} else {
									painter.current.setAttribute('stroke', v)
								}
								if (painter.shape === 'direct') {
									painter.current.setAttribute('marker-end', `url(#Triangle-${ v })`)
								}
							}
							if (painter.point1) {
								painter.point1.setAttribute('stroke', v)
							}
							if (painter.point2) {
								painter.point2.setAttribute('stroke', v)
							}
							painter.color = v;
							this.setState({ currentColor: v })
						}}
						setCurrentSize={(v, w) => {
							if (painter.current) {
								if (painter.shape === 'text') {
									painter.current.setAttribute('font-size', w + 'em')
								} else {
									painter.current.setAttribute('stroke-width', w)
								}
							}
							painter.width = w;
							this.setState({ currentSize: v })

						}} setCurrentTool={(v, w) => {
							unSelectItem(painter)
							const input = document.getElementById('svgmain_input')
							if (input) {
								input.style.visibility = 'hidden'
							}

							if (w === 'hand') {
								if (this.svgRef.current) {
									this.svgRef.current.style.pointerEvents = 'none';
								}
								if (this.mainRef.current) {
									this.mainRef.current.style.cursor = 'move'
								}
							} else {
								if (this.svgRef.current) {
									this.svgRef.current.style.pointerEvents = 'auto';
								}
								if (this.mainRef.current) {
									this.mainRef.current.style.cursor = 'auto'
								}
							}
							painter.shape = w;
							painter.current = undefined;
							painter.point1 = undefined;
							painter.point2 = undefined;
							this.setState({ currentTool: v })
						}
						}
						onDelete={() => {

							if (painter.current && painter.current.parentElement) {
								painter.current.parentElement.removeChild(painter.current)
							}
							if (painter.point1 && painter.point1.parentElement) {
								painter.point1.parentElement.removeChild(painter.point1)
							}
							if (painter.point2 && painter.point2.parentElement) {
								painter.point2.parentElement.removeChild(painter.point2)
							}

							painter.current = undefined;
							painter.point1 = undefined;
							painter.point2 = undefined;

						}}
						onFullscreen={() => {
							requestFullScreen(this.ref.current!)
						}}></Tools>
					<div
						style={{
							background: '#fff',
							margin: 0,
							height: '100%',
							textAlign: 'center',
							overflow: 'scroll',
							paddingTop: this.state.size.toolHeight,
							userSelect: 'none',
						}}>
						<div ref={this.mainRef}
							onMouseDown={this.onMouseDown}
							onMouseUp={this.onMouseUp}
							onMouseLeave={this.onMouseLeave}
							onMouseMove={this.onMouseMove}
							style={{
								transformOrigin: '0% 0%',
								transform: `scale(${ this.state.scale })`,
								position: 'relative',
							}}>
							<img onLoad={_ => {
								this.updateSize()
							}} style={{ width: '100%' }} alt='' src={this.state.page.src}
								onError={_ => {
									this.setState({ loadedImage: false })
								}}></img>
							{
								this.state.loadedImage === false ?
									<Empty></Empty> :
									<div style={{
										position: 'absolute', top: 0, left: 0, right: 0, bottom: 0,
									}}>
										<svg ref={this.svgRef} xmlns="http://www.w3.org/2000/svg" version="1.1"
											preserveAspectRatio="none"
											width='100%' height='100%'
											//viewBox的四个参数分别代表：最小X轴数值；最小y轴数值；宽度；高度。
											viewBox={`0 0 500 ${ this.state.size.height / this.state.size.sclar }`}>
											{OriginSvg()}
										</svg>
									</div>
							}

							<div contentEditable id='svgmain_input' style={{
								position: 'absolute', fontSize: '2em',
								border: '1px dashed red', outline: 'none',
								backgroundColor: 'rgba(255,255,255,0.5)', top: 0, color: 'red',
								visibility: 'hidden', minWidth: 96, textAlign: 'left'
							}} onMouseDown={e => {
								e.stopPropagation()
							}}>
							</div>
						</div>
					</div>
				</div>

			</div>

		)
	}
}

//const styles: { [key: string]: React.CSSProperties } = {
//}

export default A;