import React, { } from 'react';
import { Breadcrumb, Button, Card, message, Empty, Icon, Pagination } from 'antd';
import { SelectedItem } from '../Review';
import request from '../request';

const { Meta } = Card;

interface State {
	show: boolean
	breadcrumbs: string[]
	items: {
		pageUrl: string,
		pageName: string,
		failed?: boolean,
		pageDate: string
		pageId: number
		pageVer: number
		paperId: number
		pfold: string
	}[]
	index: number
	loading: boolean
}

class A extends React.Component<{
	select: (item: {
		pageDate: string
		pageId: number
		pageVer: number
		paperId: number
		pfold: string
	}) => void
}, State> {

	state: State = {
		show: false,
		breadcrumbs: [],
		items: [],
		index: 1,
		loading: true
	}

	hide = () => {
		this.setState({ show: false })
	}

	show = () => {
		this.setState({ show: true })
	}

	showNew = (query: SelectedItem, breadcrumbs?: string[]) => {

		this.setState({ show: true, breadcrumbs: breadcrumbs || this.state.breadcrumbs, loading: true, index: 1 })

		if (query.paperId < 0) {
			this.setState({ loading: false })
			return
		}

		request.page.getPageImageUrl({
			pageDate: query.pageDate,
			pageId: query.pageId,
			paperId: query.paperId,
			pfold: query.pfold,
			pageSize: 100000

		}).then(res => {

			setTimeout(() => {
				if (res.status && res.data.status) {
					this.setState({ items: res.data.data.rows })
				} else {
					message.error((res.data && res.data.errorMsg) || res.statusText)
				}
				this.setState({ loading: false })
			}, 300);
		})
	}

	render() {

		return (
			<div className='draw-page' style={{ top: this.state.show ? '0' : '100vh', backgroundColor: '#f0f2f5' }}>
				<div className='toolbar'>
					<Breadcrumb style={{ width: 'calc(100% - 36px)' }}>
						<Breadcrumb.Item><Icon type='home' />&nbsp;版面预览</Breadcrumb.Item>
						{
							this.state.breadcrumbs.map(v => <Breadcrumb.Item key={v}>{v}</Breadcrumb.Item>)
						}
						<Breadcrumb.Item >
							<Pagination
								size='small'
								style={{ display: 'inline', color: '#1890ff' }}
								current={this.state.index}
								total={this.state.items.length}
								showTotal={(total) => <span>总共 <b>{total}</b> 版</span>}
								onChange={e => {
									this.setState({ index: e })
								}} />
						</Breadcrumb.Item>
					</Breadcrumb>

					<Button icon='close' type='link' style={{ position: 'absolute', right: 8, top: 4, }}
						onClick={_ => this.setState({ show: false })}></Button>
				</div>
				<div style={{
					backgroundColor: '#fafafa',
					padding: '0 20px 20px 20px',
					height: 'calc(100vh - 60px)', overflow: 'scroll'
				}}>

					<div style={{ display: 'flex', flexWrap: 'wrap', alignItems: 'stretch' }}>
						{this.state.items
							.filter((v, i) => i >= this.state.index * 10 - 10 && i < this.state.index * 10)
							.map(item =>
								<Card
									key={item.pageId}
									hoverable
									style={{ margin: 12 }}
									cover={
										item.failed ?
											<div style={{ height: 300 }}><Empty /></div> :
											<img onError={e => {
													item.failed = true
													this.setState({ items: [...this.state.items] })
												}}
												alt='没找到版面' src={item.pageUrl} style={{
													height: 300,
													width:'auto'
												}}></img>}
									onClick={() => {
										this.props.select({
											pageDate: item.pageDate,
											pageId: item.pageId,
											pageVer: item.pageVer,
											paperId: item.paperId,
											pfold: item.pfold
										})

										this.setState({ show: false })
									}}>
									<Meta title={item.pageName} />
								</Card>)
						}
					</div>
				</div>
			</div>
		)
	}
}

//const styles: { [key: string]: React.CSSProperties } = {
//}

export default A;