import React, { useState, useEffect, useCallback } from 'react';
import { message, Layout, Menu, Icon, List, Checkbox, Divider, Button, Tag } from 'antd';
import request from '../request';
import { RouteComponentProps } from 'react-router';


const { Sider, Content } = Layout;
const { SubMenu } = Menu;

export interface PaperData {
	paperId: number,
	name: string,
	folds?: {
		fold: string,
		papers?: {
			paperId: number,
			pageId: number,

		}[]
	}[],
	roles?: {
		pageId: number,
		paperId: number,
		funcId: number,
		pageName: string
	}[]
}

const A: React.FC<RouteComponentProps> = (props) => {

	const [prgs, setPrgs] = useState<{ funcId: number, funcName: string }[]>([])
	const [paperData, setPaperData] = useState<PaperData[]>([])
	const [roleId] = useState(parseInt(new URLSearchParams(props.location.search).get('roleId') || '0') || 0)
	const [selectedRoles, setSelectedRoles] = useState<{ funcId: number, checked: boolean, clicked: boolean }[]>([])

	const [roleItems, setRoleItems] = useState<(number | string)[]>([])
	const [loading, setLoading] = useState(true)
	const [changed, setChanged] = useState(false)
	const [openKeys, setOpenKeys] = useState<string[]>([])
	const [selectedPages, setSelectedPages] = useState<{ paperId: number, pageId: number }[]>([])

	const [roles, setRoles] = useState<{ name: string, items: { pageId: number, pageName: string, funcs: string[] }[] }>({ name: '', items: [] })
	const [funName, setFunName] = useState<{ [key: number]: string }>({})

	/**
	 * 获取报纸
	 */
	const getPaper = useCallback((fouce: boolean = false) => {

		if (!fouce && paperData.length > 0) {
			return
		}

		request.paper.getPaperInfo({
			pageIndex: 1,
			pageSize: 100
		}).then(res => {

			if (res.status && res.data.status) {
				setPaperData(res.data.data.rows)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [paperData.length])

	const getRole = useCallback((paperId: number) => {

		const p = paperData.find(v => v.paperId === paperId)

		if (p && p.roles) {
			return
		}
		setLoading(true)
		request.roleRight.getRoleRight({ paperId, roleId, pageIndex: 1, pageSize: 1000000, pageId: -1 }).then(res => {
			setLoading(false)
			if (res.status && res.data.status) {

				if (p) {
					p.roles = res.data.data.rows
					setPaperData([...paperData])
				}

			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [paperData, roleId])

	/**
	 * 获取叠次
	 * @param paperId 报纸ID
	 */
	const getFold = useCallback((paperId: number) => {

		let i = paperData.findIndex(v => v.paperId === paperId)

		if (-1 === i || paperData[i].folds !== undefined) {
			return
		}

		request.fold.getPaperFold({
			pageIndex: 1,
			pageSize: 100,
			paperId
		}).then(res => {

			if (res.status && res.data.status) {

				paperData[i].folds = res.data.data.rows

				setPaperData([...paperData])
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [paperData])

	/**
	 * 获取版面
	 * @param paperId 报纸ID
	 * @param fold 叠次ID
	 */
	const getPage = useCallback((paperId: number, fold: string) => {
		const i = paperData.findIndex(v => v.paperId === paperId)

		if (-1 === i || paperData[i].folds === undefined) {
			return
		}

		const j = paperData[i].folds!.findIndex(sv => sv.fold === fold)

		if (-1 === j || paperData[i].folds![j].papers !== undefined) {
			return
		}

		request.page.getPaperPage({
			pageIndex: 1,
			pageSize: 100,
			paperId,
			fold
		}).then(res => {

			if (res.status === 200 && res.data.status) {

				paperData[i].folds![j].papers = res.data.data.rows
				setPaperData([...paperData])
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [paperData])

	/**
	 * 更改权限
	 * @param index 
	 * @param selected 
	 */
	const selectRole = (index: number, selected: boolean) => {

		if (selected) {
			selectedRoles[index].checked = true
			selectedRoles[index].clicked = true
		} else {
			selectedRoles[index].checked = false
			selectedRoles[index].clicked = true
		}

		setSelectedRoles([...selectedRoles])
		setChanged(true)
	}

	const saveRole = () => {

		setLoading(true)
		setOpenKeys([])
		request.roleRight.resetRoleRight({
			funcData: selectedRoles.filter(v => v.checked).map(v => v.funcId),
			pageData: selectedPages,
			roleId
		}).then(res => {

			if (res.status) {
				getPaper(true)

				setSelectedPages([])
				message.success('权限修改已保存！')
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
			setLoading(false)
		})
	}

	useEffect(() => {

		if (!paperData.length || !prgs.length || !changed) {
			return
		}

		/**
		 * 选中版面为零，还原选中状态
		 */
		if (!selectedPages.length) {
			selectedRoles.forEach(v => { v.checked = false; v.clicked = false })
		}

		let nr = selectedRoles.filter(v => !v.clicked)
		nr.forEach(v => v.checked = false)
		/**
		 * 记录权限对应是否选中
		 */
		selectedPages.forEach(r => {
			paperData.forEach(sv => {
				sv.roles && sv.roles.filter(rl => rl.pageId === r.pageId).forEach(rl => {

					const item = nr.find(sv => sv.funcId === rl.funcId)
					if (item) {
						item.checked = true
					}
				})
			})
		})

		setSelectedRoles([...selectedRoles])
		setChanged(false)

	}, [changed, paperData, prgs, selectedPages, selectedRoles])

	useEffect(() => {
		getPaper()

		request.prgfunc.getAllPrgFunc().then(res => {
			if (res.status && res.data.status) {
				setSelectedRoles(res.data.data.rows.map((v: any) => ({ funcId: v.funcId, count: 0, clicked: false })))
				setPrgs(res.data.data.rows)
				let items: { [key: number]: string } = {}
				res.data.data.rows.forEach((v: any) => {
					items[v.funcId] = v.funcName
				})
				setFunName(items)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})

	}, [getPaper])

	useEffect(() => {

		const p = paperData.find(v => v.paperId === roleItems[0])

		if (!p) {
			return
		}

		const rules = p.roles || []
		let funcs: { pageId: number, pageName: string, funcs: string[] }[] = []

		/**
		 * 选择报纸
		 */
		if (roleItems.length === 1) {

			getFold(roleItems[0] as number)
			getRole(roleItems[0] as number)

			rules.forEach(v => {
				const name = funName[v.funcId] || ''
				const item = funcs.find(sv => sv.pageId === v.pageId)
				if (item) {
					item.funcs.push(name)
				} else {
					funcs.push({ pageName: v.pageName, pageId: v.pageId, funcs: [name] })
				}
			})

			setRoles({ name: p.name || '', items: funcs })
		}
		/**
		 * 选择报纸>叠次
		 */
		if (roleItems.length === 2) {
			getPage(roleItems[0] as number, roleItems[1] as string)

			const item = p.folds && p.folds.find(v => v.fold === roleItems[1] as string)
			if (item && item.papers) {
				const ids = item.papers.map(v => v.pageId)
				rules.filter(v => ids.some(sv => sv === v.pageId)).forEach(v => {
					const name = funName[v.funcId] || ''
					const item = funcs.find(sv => sv.pageId === v.pageId)
					if (item) {
						item.funcs.push(name)
					} else {
						funcs.push({ pageName: v.pageName, pageId: v.pageId, funcs: [name] })
					}
				})
			}

			setRoles({ name: (p.name || '') + ' / ' + roleItems[1], items: funcs })
		}
		/**
		 * 选择报纸>叠次>版面
		 */
		if (roleItems.length === 3) {

			rules.filter(v => v.pageId === roleItems[2] as number).forEach(v => {
				const name = funName[v.funcId] || ''
				const item = funcs.find(sv => sv.pageId === v.pageId)
				if (item) {
					item.funcs.push(name)
				} else {
					funcs.push({ pageName: v.pageName, pageId: v.pageId, funcs: [name] })
				}
			})

			setRoles({ name: (p.name || '') + ' / ' + roleItems[1], items: funcs })
		}

	}, [funName, getFold, getPage, getRole, paperData, roleItems])

	return (
		<Layout style={{ height: 'calc(100vh - 150px)' }}>
			<Sider theme='light' width={320} style={{ height: '100%', overflowY: 'scroll' }}>
				<Menu mode="inline" style={{ borderRight: 'none' }} openKeys={openKeys} selectable={false}>

					{paperData.map(v =>
						<SubMenu key={'1_' + v.paperId} title={<span
							className={roleItems.length === 1 && roleItems[0] === v.paperId ? 'role-menu-selected' : ''}
							onClick={e => {
								e.stopPropagation()
								if (roleItems.length === 1 && roleItems[0] === v.paperId) {
									setRoleItems([])
								} else {
									setRoleItems([v.paperId])

								}

							}}><Icon type="read" /><span>{v.name}</span></span>}
							onTitleClick={e => {
								if (openKeys.includes(e.key)) {
									setOpenKeys(openKeys.filter(v => v !== e.key))
								} else {
									setOpenKeys([...openKeys, e.key])
								}
								getFold(v.paperId)
								getRole(v.paperId)
							}}>

							{v.folds && v.folds.map(sv =>
								<SubMenu key={'2_' + sv.fold}
									title={<>
										<span
											className={roleItems.length === 2 && roleItems[1] === sv.fold ? 'role-menu-selected' : ''}
											onClick={e => {
												e.stopPropagation()

												if (roleItems.length === 2 && roleItems[1] === sv.fold) {
													setRoleItems([])
												} else {
													setRoleItems([v.paperId, sv.fold])
												}

											}}><Icon type="apartment" /><span >{sv.fold}</span></span>
										<Checkbox style={{ float: 'right' }}
											disabled={!v.roles || !sv.papers || !sv.papers.length}
											checked={
												sv.papers &&
												sv.papers.length !== 0 &&
												sv.papers.some(v => selectedPages.some(sv => sv.pageId === v.pageId))}
											indeterminate={
												sv.papers &&
												sv.papers.some(v => selectedPages.some(sv => sv.pageId === v.pageId)) &&
												sv.papers.filter(v => selectedPages.some(sv => sv.pageId === v.pageId)).length < sv.papers.length}
											onClick={e => {
												e.stopPropagation()
											}}
											onChange={e => {
												e.stopPropagation();

												if (sv.papers) {

													if (e.target.checked) {
														const old = sv.papers.filter(v => !selectedPages.some(sv => sv.pageId === v.pageId));

														setSelectedPages([...selectedPages,
														...old.map(v => ({
															pageId: v.pageId,
															paperId: v.paperId
														}))])

													} else {

														setSelectedPages(selectedPages.filter(v => !sv.papers!.some(ssv => ssv.pageId === v.pageId)))
													}
													setChanged(true)
												}
											}}></Checkbox>
									</>}
									onTitleClick={e => {
										if (openKeys.includes(e.key)) {
											setOpenKeys(openKeys.filter(v => v !== e.key))
										} else {
											setOpenKeys([...openKeys, e.key])
										}
										getPage(v.paperId, sv.fold)
									}}>

									{sv.papers && sv.papers.map((ssv: any) =>
										<Menu.Item key={'3_' + ssv.pageId}>
											<span
												className={roleItems.length === 3 && roleItems[2] === ssv.pageId ? 'role-menu-selected' : ''}
												onClick={e => {
													e.stopPropagation()
													if (roleItems.length === 3 && roleItems[2] === ssv.pageId) {
														setRoleItems([])
													} else {
														setRoleItems([v.paperId, sv.fold, ssv.pageId])
													}

												}}><Icon type="file" />{ssv.name}</span>
											<Checkbox style={{ float: 'right' }}
												disabled={!v.roles}
												checked={selectedPages.some(v => v.pageId === ssv.pageId)}
												onClick={e => {
													e.stopPropagation()
												}}
												onChange={e => {
													if (sv.papers) {

														if (e.target.checked) {
															setSelectedPages([...selectedPages, { pageId: ssv.pageId, paperId: ssv.paperId }])
														} else {
															setSelectedPages(selectedPages.filter(v => ssv.pageId !== v.pageId))
														}

														setChanged(true)
													}

												}}></Checkbox>
										</Menu.Item>)
									}
									{
										(!sv.papers || sv.papers.length === 0) &&
										<Menu.Item key="1" disabled>没有找到版面！<Icon type='search' /></Menu.Item>
									}
								</SubMenu>)
							}
							{
								(!v.folds || v.folds.length === 0) &&
								<Menu.Item key="1" disabled>没有找到叠次！<Icon type='search' /></Menu.Item>
							}
						</SubMenu>)
					}

				</Menu>
			</Sider >
			<Content style={{ height: '100%', overflow: 'scroll', backgroundColor: '#fff', padding: 20, borderLeft: '1px solid #ddd' }}>
				<List
					grid={{
						gutter: 24,
						xs: 1,
						sm: 2,
						md: 2,
						lg: 3,
						xl: 4,
						xxl: 6,
					}}
					itemLayout="horizontal"
					size='small'
					header={<div>权限设置 <Divider type='vertical'></Divider>
						<Button disabled={loading || selectedPages.length === 0}
							onClick={saveRole}
							type='primary' icon='save' size='small'>保存更改</Button>
					</div>}
					dataSource={prgs}
					renderItem={(item, index) => (
						<List.Item>
							<List.Item.Meta
								title={item.funcName}
								avatar={<Checkbox disabled={loading || selectedPages.length === 0}
									checked={selectedRoles[index].checked} onChange={e => {
										selectRole(index, e.target.checked)
									}}></Checkbox>} />
						</List.Item>
					)} />
				<Divider></Divider>
				<List header={<span>查看权限 <Divider type='vertical'></Divider> {roles.name}</span>}
					dataSource={roles.items}
					renderItem={item => <List.Item key={item.pageId}>
						<span style={{ minWidth: 120, textAlign: 'center', fontWeight: 'bold' }}>{item.pageName}</span><Divider type='vertical'></Divider>
						<span>{item.funcs.map((v, i) => <Tag key={i} color='blue'>{v}</Tag>)}</span>
					</List.Item>}>

				</List>
			</Content>
		</Layout >
	)
}

export default A;

