import React, { useState, useEffect, useCallback } from 'react';
import { Table, Button, Modal, Switch, Typography, message, Popconfirm } from 'antd';
import PForm from './forms/paper';
import request from '../request';
import { RouteChildrenProps } from 'react-router';
import { history } from '../index'
import { Link } from 'react-router-dom';
import store from '../store';

const A: React.FC<RouteChildrenProps> = (props) => {

	const [data, setData] = useState({
		pageIndex: 1,
		pageSize: 10,
		total: 0,
		rows: []
	})
	const [showForm, setShowForm] = useState(false)

	const columns: any[] = [
		{
			title: '报纸名称', dataIndex: 'name', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					if (v.trim().length === 0) {
						message.error('报纸名不能为空！')
						return
					}

					saveData({ ...item, name: v })
				}
			}}>{text}</Typography.Paragraph>
		},
		{
			title: '叠次', key: 'action1', width: 120, align: 'center',
			render: (v: any) => <Link to={`/fold?paperId=${ v.paperId }`} onClick={_ => {

				const path = history.location.pathname + history.location.search

				store.dispatch({ type: 'paper', data: { name: v.name, path: path } })

			}}>查看叠次</Link>
		},
		{
			title: '报纸代码', dataIndex: 'code', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					if (v.trim().length === 0) {
						message.error('报纸代码不能为空！')
						return
					}
					saveData({ ...item, code: v })
				}
			}}>{text}</Typography.Paragraph>
		},
		{
			title: '创建日期', dataIndex: 'createDate', width: 160, align: 'center',
		},
		{
			title: '是否停发', dataIndex: 'isStop', width: 160, align: 'center',
			render: (v: number, item: any) => <Switch defaultChecked={v === 1} onChange={v => {
				saveData({ ...item, isStop: v ? 1 : 0 })
			}}></Switch>
		},
		{
			title: '报纸角色', dataIndex: 'psNameRule', width: 160,
		},

		{
			title: '编辑', key: 'action2', width: 80, align: 'center',
			render: (v: any) => <Popconfirm
				title="确定要删除该报纸吗?"
				onConfirm={_ => {
					request.paper.deletePaperInfo(v).then(res => {
						if (res.status && res.data.status) {
							message.success('报纸已删除！')
							updateData()
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
					})
				}}
			>
				<Button type='danger' >删除</Button>
			</Popconfirm>
		},
	];

	/**
	 * 保存修改项
	 * @param item 
	 */
	const saveData = (item: any) => {
		request.paper.saveData(item).then(res => {
			if (res.status && res.data.status) {
				message.success('报纸信息已保存！')
				updateData()
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}

	/**
	 * 更新列表数据
	 */
	const updateData = useCallback(() => {
		const s = new URLSearchParams(props.location.search)
		const search = {
			pageIndex: parseInt(s.get('pageIndex') || '1'),
			pageSize: parseInt(s.get('pageSize') || '10')
		}
		request.paper.getPaperInfo(search).then(res => {

			if (res.status && res.data.status) {
				setData(res.data.data)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [props.location.search])

	useEffect(() => {
		updateData()
	}, [updateData])

	return (
		<div>
			<div style={{ padding: '12px 16px', borderBottom: '6px solid #eee' }}>
				<Button type='primary' icon='plus' onClick={_ => setShowForm(true)}>添加报纸</Button>
			</div>
			<Table dataSource={data.rows.map((v: any) => ({ ...v, key: v.paperId }))}
				columns={columns}
				scroll={{ x: 960 }}
				pagination={{
					showSizeChanger: true,
					current: data.pageIndex,
					total: data.total,
					pageSize: data.pageSize,
					pageSizeOptions: ['10', '20', '40']
				}}
				onChange={v => {
					const s = new URLSearchParams(props.location.search)
					if (v.pageSize) {
						s.set('pageSize', v.pageSize + '')
					}
					if (v.current) {
						s.set('pageIndex', v.current + '')
					}

					history.push(props.location.pathname + '?' + s.toString())

				}} />
			<Modal
				title='新建报纸'
				visible={showForm}
				closable
				maskClosable={false}
				footer={null}
				onCancel={_ => setShowForm(false)}>
				<PForm submit={(v) => {
					request.paper.saveData(v).then(res => {
						if (res.status && res.data.status) {
							message.success('报纸添加完成！')
							updateData()
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
						setShowForm(false);
					})

				}}></PForm>
			</Modal>
		</div>
	)
}

//const styles: { [key: string]: React.CSSProperties } = {
//}

export default A;