import React, { useState, useEffect, useCallback } from 'react';
import { Table, Button, Modal, Typography, message, Popconfirm } from 'antd';
import PForm from './forms/dept';
import request from '../request';
import { RouteChildrenProps } from 'react-router';
import { history } from '../index'
import { Link } from 'react-router-dom';
import store from '../store';

const A: React.FC<RouteChildrenProps> = (props) => {

	const [data, setData] = useState({
		pageIndex: 1,
		pageSize: 10,
		total: 0,
		rows: []
	})
	const [showForm, setShowForm] = useState(false)

	const columns: any[] = [
		{
			title: '部门名称', dataIndex: 'name', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					if (v.trim().length === 0) {
						message.error('部门名不能为空！')
						return
					}

					saveData({ ...item, name: v })
				}
			}}>{text}</Typography.Paragraph>
		},
		{
			title: '人员', key: 'action1', width: 120, align: 'center',
			render: (v: any) => <Link to={`/employee?deptId=${ v.deptId }`} onClick={_ => {

				const path = history.location.pathname + history.location.search

				store.dispatch({ type: 'paper', data: { name: v.name, path: path } })

			}}>查看人员</Link>
		},
		{
			title: '部门代码', dataIndex: 'code', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					if (v.trim().length === 0) {
						message.error('部门代码不能为空！')
						return
					}
					saveData({ ...item, code: v })
				}
			}}>{text}</Typography.Paragraph>
		},
		{
			title: '联系人', dataIndex: 'who', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					saveData({ ...item, who: v })
				}
			}}>{text || ''}</Typography.Paragraph>
		},
		{
			title: '电话', dataIndex: 'phone', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					saveData({ ...item, code: v })
				}
			}}>{text || ''}</Typography.Paragraph>
		},
		{
			title: '邮箱', dataIndex: 'email', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					saveData({ ...item, code: v })
				}
			}}>{text || ''}</Typography.Paragraph>
		},
		{
			title: '传真', dataIndex: 'fax', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					saveData({ ...item, code: v })
				}
			}}>{text || ''}</Typography.Paragraph>
		},

		{
			title: '编辑', key: 'action2', width: 80, align: 'center',
			render: (v: any) => <Popconfirm
				title="确定要删除该部门吗?"
				onConfirm={_ => {
					request.dept.deleteDeptInfo(v).then(res => {
						if (res.status && res.data.status) {
							message.success('部门已删除！')
							updateData()
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
					})
				}}>
				<Button type='danger' >删除</Button>
			</Popconfirm>

		},
	];

	/**
	 * 保存修改项
	 * @param item 
	 */
	const saveData = (item: any) => {
		request.dept.updateDeptInfo(item).then(res => {
			if (res.status && res.data.status) {
				message.success('部门信息已保存！')
				updateData()
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}

	/**
	 * 更新列表数据
	 */
	const updateData = useCallback(() => {
		const s = new URLSearchParams(props.location.search)

		const search = {
			pageIndex: parseInt(s.get('pageIndex') || '1'),
			pageSize: parseInt(s.get('pageSize') || '10')
		}
		request.dept.getDeptInfo(search).then(res => {

			if (res.status && res.data.status) {
				setData(res.data.data)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [props.location.search])

	useEffect(() => {
		updateData()
	}, [updateData])

	return (
		<div>
			<div style={{ padding: '12px 16px', borderBottom: '6px solid #eee' }}>
				<Button type='primary' icon='plus' onClick={_ => setShowForm(true)}>添加部门</Button>
			</div>
			<Table dataSource={data.rows.map((v: any) => ({ ...v, key: v.deptId }))}
				columns={columns}
				scroll={{ x: 960 }}
				pagination={{
					showSizeChanger: true,
					current: data.pageIndex,
					total: data.total,
					pageSize: data.pageSize,
					pageSizeOptions: ['10', '20', '40']
				}}
				onChange={v => {
					const s = new URLSearchParams(props.location.search)
					if (v.pageSize) {
						s.set('pageSize', v.pageSize + '')
					}
					if (v.current) {
						s.set('pageIndex', v.current + '')
					}

					history.push(props.location.pathname + '?' + s.toString())

				}} />
			<Modal
				title='新建部门'
				visible={showForm}
				closable
				maskClosable={false}
				footer={null}
				onCancel={_ => setShowForm(false)}>
				<PForm submit={(v) => {
					request.dept.newDeptInfo(v).then(res => {

						if (res.status && res.data.status) {
							message.success('部门添加完成！')
							updateData()
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
						setShowForm(false)
					})

				}}></PForm>
			</Modal>
		</div>
	)
}

export default A;