import React, { useState, useEffect, useCallback, createRef } from 'react';
import { Table, Button, Modal, Typography, message, Popconfirm, Icon, Input, Switch } from 'antd';
import PForm from './forms/employee';
import request from '../request';
import { RouteChildrenProps } from 'react-router';
import { history } from '../index';
import { Link } from 'react-router-dom';
import store from '../store';

const A: React.FC<RouteChildrenProps> = (props) => {

	const [data, setData] = useState({
		pageIndex: 1,
		pageSize: 10,
		total: 0,
		rows: []
	})
	const [showForm, setShowForm] = useState(false)

	const columns: any[] = [
		{
			title: '登录名', dataIndex: 'loginId', width: 160
		},
		{
			title: '角色', key: 'roleId2', dataIndex: 'loginId', width: 120, align: 'center',
			render: (text: string, v: any) => <Link to={`/employeeRole?loginId=${ text }`} onClick={_ => {

				const path = history.location.pathname + history.location.search

				store.dispatch({ type: 'fold', data: { name: v.loginId, path: path } })

			}}>查看角色</Link>
		},
		{
			title: '登录密码', dataIndex: 'password', width: 160,
			render: (text: string, item: any) => {
				const rf1 = createRef<HTMLSpanElement>();
				const rf2 = createRef<HTMLSpanElement>();
				return <div>
					<span ref={rf1}>******<Icon type="edit" onClick={e => {
						if (rf1.current) {
							rf1.current.style.display = 'none';
						}
						if (rf2.current) {
							rf2.current.style.display = 'inline';
						}
					}} /></span>
					<span ref={rf2} style={{ display: 'none' }}><Input.Password size='small' onPressEnter={e => {
						if (rf1.current) {
							rf1.current.style.display = 'inline';
						}
						if (rf2.current) {
							rf2.current.style.display = 'none';
						}
						saveData({ ...item, password: e.currentTarget.value })
					}} onBlur={e => {
						if (rf1.current) {
							rf1.current.style.display = 'inline';
						}
						if (rf2.current) {
							rf2.current.style.display = 'none';
						}
						saveData({ ...item, password: e.currentTarget.value })
					}}></Input.Password></span>
				</div >
			}
		},

		{
			title: '昵称', dataIndex: 'userName', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					saveData({ ...item, userName: v })
				}
			}}>{text || ''}</Typography.Paragraph>
		},
		{
			title: '电话', dataIndex: 'phone', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					saveData({ ...item, phone: v })
				}
			}}>{text || ''}</Typography.Paragraph>
		},
		{
			title: '邮箱', dataIndex: 'email', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					saveData({ ...item, email: v })
				}
			}}>{text || ''}</Typography.Paragraph>
		},
		{
			title: '传真', dataIndex: 'fax', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					saveData({ ...item, fax: v })
				}
			}}>{text || ''}</Typography.Paragraph>
		},
		{
			title: '是否在职', dataIndex: 'onWork', width: 160, align: 'center',
			render: (v: number, item: any) => <Switch defaultChecked={v === 1} onChange={v => {
				saveData({ ...item, onWork: v ? 1 : 0 })
			}}></Switch>
		},
		{
			title: '编辑', key: 'action2', width: 80, align: 'center',
			render: (v: any) => <Popconfirm
				title="确定要删除该人员吗?"
				onConfirm={_ => {
					request.dept.deleteDeptInfo(v).then(res => {
						if (res.status && res.data.status) {
							message.success('人员已删除！')
							updateData()
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
					})
				}}>
				<Button type='danger' >删除</Button>
			</Popconfirm>
		},
	];

	/**
	 * 保存修改项
	 * @param item 
	 */
	const saveData = (item: any) => {
		request.employee.updateDeptInfo(item).then(res => {
			if (res.status && res.data.status) {
				message.success('人员信息已保存！')
				updateData()
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}

	/**
	 * 更新列表数据
	 */
	const updateData = useCallback(() => {
		const s = new URLSearchParams(props.location.search)

		const search = {
			deptId: parseInt(s.get('deptId') || '0'),
			pageIndex: parseInt(s.get('pageIndex') || '1'),
			pageSize: parseInt(s.get('pageSize') || '10')
		}
		request.employee.getDeptEmployee(search).then(res => {

			if (res.status && res.data.status) {
				setData(res.data.data)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [props.location.search])

	useEffect(() => {
		updateData()
	}, [updateData])

	return (
		<div>
			<div style={{ padding: '12px 16px', borderBottom: '6px solid #eee' }}>
				<Button type='primary' icon='plus' onClick={_ => setShowForm(true)}>添加人员</Button>
			</div>
			<Table dataSource={data.rows.map((v: any) => ({ ...v, key: v.loginId }))}
				columns={columns}
				scroll={{ x: 1024 }}
				pagination={{
					showSizeChanger: true,
					current: data.pageIndex,
					total: data.total,
					pageSize: data.pageSize,
					pageSizeOptions: ['10', '20', '40']
				}}
				onChange={v => {
					const s = new URLSearchParams(props.location.search)
					if (v.pageSize) {
						s.set('pageSize', v.pageSize + '')
					}
					if (v.current) {
						s.set('pageIndex', v.current + '')
					}

					history.push(props.location.pathname + '?' + s.toString())

				}} />
			<Modal
				title='新建人员'
				visible={showForm}
				closable
				maskClosable={false}
				footer={null}
				onCancel={_ => setShowForm(false)}>
				<PForm submit={(v) => {

					const s = new URLSearchParams(props.location.search)
					request.employee.newDeptEmployee({ ...v, deptId: parseInt(s.get('deptId') || '0') }).then(res => {

						if (res.status && res.data.status) {
							message.success('人员添加完成！')
							updateData()
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
						setShowForm(false)
					})

				}}></PForm>
			</Modal>
		</div>
	)
}

export default A;