
import { createStore, combineReducers } from 'redux';
import { onFullScreenEvent } from './funcs';

function paper(state = { paper: { name: '', path: '' }, fold: { name: '', path: '' } },
	action: { type: 'paper' | 'fold' | 'clean', data: any }) {

	switch (action.type) {

		case 'paper':
			return { paper: action.data, fold: { name: '', path: '' } }
		case 'fold':
			return { paper: state.paper, fold: action.data }

		default:
			return { paper: { name: '', path: '' }, fold: { name: '', path: '' } };
	}
}

function fullScreen(state = false, action: { type: 'fullScreen' | 'exit' }) {
	switch (action.type) {
		case 'exit':
			return false
		case 'fullScreen':
			return true
		default: return false
	}
}

const store = createStore(combineReducers({ paper, fullScreen }));

export default store;


onFullScreenEvent(bk => {
	if (bk) {
		store.dispatch({ type: 'fullScreen' })
	} else {
		store.dispatch({ type: 'exit' })
	}
})