import React from 'react';
import './App.css';
import { Layout, Menu, Breadcrumb,Icon } from 'antd';
import { Route, Switch, RouteComponentProps, Link } from 'react-router-dom';

import Paper from './pages/paper';
import Dept from './pages/dept';
import Employee from './pages/employee';
import Role from './pages/role';
import RoleRight from './pages/roleRight';
import RoleEmployee from './pages/roleEmployee';
import EmployeeRole from './pages/employeeRole';

import Fold from './pages/fold';
import Page from './pages/page';

import { history } from '.';
import { connect } from 'react-redux';
const { Header, Content } = Layout;

interface Props extends RouteComponentProps {
	paper: {
		paper: {
			name: string
			path: string
		},
		fold: {
			name: string
			path: string
		},
	},
	dispatch: (act: { type: string, data: string }) => void
}

class App extends React.Component<Props> {

	componentDidMount() {
		if (this.props.location.pathname === '/') {

			history.replace('/paper')
		}
	}

	render() {

		return (
			<Layout>
				<Header style={{
					position: 'fixed', zIndex: 1,
					width: '100%', backgroundColor: '#fff',
					padding: 0
				}}>
					<Menu
						theme="light"
						mode="horizontal"
						selectedKeys={[history.location.pathname]}
						style={{ lineHeight: '64px', padding: '0 64px', fontSize: 'large', fontWeight: 'bold' }}
						onSelect={v => {
							history.push(v.key);
							this.props.dispatch({ type: 'clean', data: '' });
						}}
					>
						<Menu.Item key="/paper">报纸</Menu.Item>
						<Menu.Item key="/dept">部门</Menu.Item>
						<Menu.Item key="/role">角色</Menu.Item>

					</Menu>
				</Header>
				<Content style={{ marginTop: 64 }}>

					<div >
						<Breadcrumb style={{ padding: '16px 20px', backgroundColor: '#fff' }}>
							<Breadcrumb.Item><Icon type='home' />&nbsp;浏览历史</Breadcrumb.Item>
							{
								this.props.paper.paper.name !== '' &&
								<Breadcrumb.Item><Link to={this.props.paper.paper.path} onClick={_ => {

								}}>{this.props.paper.paper.name}</Link></Breadcrumb.Item>
							}
							{
								this.props.paper.fold.name !== '' &&
								<Breadcrumb.Item><Link to={this.props.paper.fold.path} onClick={_ => {

								}}>{this.props.paper.fold.name}</Link></Breadcrumb.Item>
							}
						</Breadcrumb>
					</div>

					<div style={{ background: '#fff', margin: 16, minHeight: '80vh' }}>

						<Switch>
							<Route path='/paper' component={Paper}></Route>
							<Route path='/employee' component={Employee}></Route>
							<Route path='/dept' component={Dept}></Route>
							<Route path='/role' component={Role}></Route>
							<Route path='/fold' component={Fold}></Route>
							<Route path='/page' component={Page}></Route>
							<Route path='/roleRight' component={RoleRight}></Route>
							<Route path='/roleEmployee' component={RoleEmployee}></Route>
							<Route path='/employeeRole' component={EmployeeRole}></Route>
						</Switch>
					</div>
				</Content>
			</Layout>
		);
	}
}

export default connect((state: any) => ({ paper: state.paper }))(App);
