import React, { useState, useCallback, useEffect } from 'react';
import { Table, Button, Modal, Popconfirm, message, Typography } from 'antd';
import PForm from './forms/fold';
import request from '../request';
import { RouteChildrenProps } from 'react-router';
import { history } from '../index'
import { Link } from 'react-router-dom';
import store from '../store';

const A: React.FC<RouteChildrenProps> = (props) => {

	const [data, setData] = useState({
		pageIndex: 1,
		pageSize: 10,
		total: 0,
		rows: []
	})
	const [showForm, setShowForm] = useState(false)

	const columns: any[] = [
		{
			title: '叠次名称', dataIndex: 'fold', width: 200,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					if (v.trim().length === 0) {
						message.error('叠次名不能为空！')
						return
					}

					saveData({ ...item, fold: v })
				}
			}}>{text}</Typography.Paragraph>
		},
		{
			title: '版面', key: 'action1', width: 120, align: 'center',
			render: (v: any) => <Link to={`/page?paperId=${ v.paperId }&fold=${ encodeURI(v.fold) }`} onClick={_ => {

				const path = history.location.pathname + history.location.search

				store.dispatch({ type: 'fold', data: { name: v.fold, path: path } })

			}}>查看版面</Link>
		},
		{
			title: '叠次代码', dataIndex: 'foldCode', width: 120,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					if (v.trim().length === 0) {
						message.error('叠次代码不能为空！')
						return
					}

					saveData({ ...item, foldCode: v })
				}
			}}>{text}</Typography.Paragraph>
		},
		{
			title: '编辑', key: 'action2', width: 120, align: 'center',
			render: (v: any) => <Popconfirm
				title="确定要删除该叠次吗?"
				onConfirm={_ => {
					request.fold.deletePaperFold(v).then(res => {
						if (res.status && res.data.status) {
							message.success('叠次已删除！')
							updateData()
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
					})
				}}
			>
				<Button type='danger' >删除</Button>
			</Popconfirm>
		},
	];

	/**
	 * 保存修改项
	 * @param item 
	 */
	const saveData = (item: any) => {
		request.fold.updatePaperFold(item).then(res => {

			if (res.status && res.data.status) {
				message.success('叠次信息已保存！')
				updateData()
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}

	const updateData = useCallback(() => {
		const s = new URLSearchParams(props.location.search)
		const search = {
			paperId: parseInt(s.get('paperId') || '0'),
			pageIndex: parseInt(s.get('pageIndex') || '1'),
			pageSize: parseInt(s.get('pageSize') || '10')
		}
		request.fold.getPaperFold(search).then(res => {

			if (res.status && res.data.status) {
				setData(res.data.data)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [props.location.search])

	useEffect(() => {
		updateData()

	}, [updateData])

	return (
		<div>
			<div style={{ padding: '12px 16px', borderBottom: '6px solid #eee' }}>
				<Button type='primary' icon='plus' onClick={e => setShowForm(true)}>添加叠次</Button>
			</div>
			<Table dataSource={data.rows.map((v: any) => ({ ...v, key: v.fold }))} columns={columns}
				pagination={{
					showSizeChanger: true,
					current: data.pageIndex,
					total: data.total,
					pageSize: data.pageSize,
					pageSizeOptions: ['10', '20', '40']
				}}
				onChange={v => {
					const s = new URLSearchParams(props.location.search)
					if (v.pageSize) {
						s.set('pageSize', v.pageSize + '')
					}
					if (v.current) {
						s.set('pageIndex', v.current + '')
					}

					history.push(props.location.pathname + '?' + s.toString())

				}} />
			<Modal
				title='新建报纸'
				visible={showForm}
				closable
				maskClosable={false}
				footer={null}
				onCancel={_ => setShowForm(false)}>
				<PForm submit={(v) => {

					const s = new URLSearchParams(props.location.search)

					request.fold.newPaperFold({ ...v, paperId: parseInt(s.get('paperId') || '0') }).then((res) => {
						if (res.status && res.data.status) {
							updateData()
							message.success('叠次添加完成。')
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
						setShowForm(false);
					})

				}}></PForm>
			</Modal>
		</div>
	)
};

export default A;