import React, { createRef } from 'react';
import { Empty } from 'antd';
import Tools from '../cmps/tools';

import { requestFullScreen, cancelFullScreen } from '../funcs';


interface Props {

	menuIndex: number
	loadedImage: boolean,
	show: boolean
	scale: number
	page: {
		src: string,
		svg: string
	},
	size: {
		width: number,
		height: number,

		toolHeight: number,
		sclar: number
	}
}

class A extends React.Component<any, Props> {

	state: Props = {
		menuIndex: -1,
		scale: 1,
		show: false,
		loadedImage: true,
		size: { width: 0, height: 0, toolHeight: 0, sclar: 1 },
		page: { src: '', svg: '' },
	}

	mainRef = createRef<HTMLDivElement>()
	toolsRef = createRef<HTMLDivElement>()
	svgRef = createRef<SVGSVGElement>()
	ref = createRef<HTMLDivElement>()

	/**
	 * 窗口大小改变，更新 svg 位置
	 */
	updateSize = () => {

		const rt = this.mainRef.current && this.mainRef.current.getClientRects()
		const rtTool = this.toolsRef.current && this.toolsRef.current.getClientRects()

		if (rt && rtTool && rt.length && rtTool.length) {
			this.setState({
				size: {
					width: rt[0].width,
					height: rt[0].height,
					toolHeight: rtTool[0].height,
					sclar: rt[0].width / 500
				},
				loadedImage: true
			})
		}
	}

	/**
	 * 取消全屏并隐藏
	 */
	hide = () => {
		cancelFullScreen()
		this.setState({ show: false, menuIndex: -1 })
	}

	/**
	 * 展示
	 */
	show = (item: any) => {

		if (this.state.show && this.state.page.src === item.pageUrl && this.state.page.svg === item.pageSvgInfo) {
			this.hide()
			return false
		}

		this.setState({
			scale: 1,
			show: true,
			page: {
				src: item.pageUrl,
				svg: item.pageSvgInfo
			}
		})

		if (this.svgRef.current) {
			this.svgRef.current.innerHTML = item.pageSvgInfo
		}

		return true
	}

	componentDidMount() {
		this.updateSize()
		window.addEventListener('resize', this.updateSize)

		return () => {
			window.removeEventListener('resize', this.updateSize)
		}
	}

	render() {
		return (
			<div className='draw-page' style={{ top: this.state.show ? '0' : '100vh' }}>
				<div ref={this.ref} style={{ height: '100vh', width: '100%', position: 'relative' }}>
					<Tools
						draw={false}
						disabled={!this.state.loadedImage}
						refM={this.toolsRef}
						currentColor=''
						currentSize=''
						currentTool=''
						save={() => { }}
						close={this.hide}
						scale={v => {
							if (v) {
								this.setState({ scale: this.state.scale * 1.5 }, () => {
									this.updateSize()
								})
							} else {
								if (this.state.scale > 1) {
									this.setState({ scale: this.state.scale / 1.5 }, () => {
										this.updateSize()
									})
								}
							}
						}}
						setColor={v => { }}
						setCurrentSize={(v, w) => {
						}} setCurrentTool={(v, w) => {
						}}
						onDelete={() => { }}
						onFullscreen={() => {
							requestFullScreen(this.ref.current!)
						}}></Tools>
					<div
						style={{
							background: '#fff',
							margin: 0,
							height: '100vh',
							textAlign: 'center',
							overflow: 'scroll',
							paddingTop: this.state.size.toolHeight,
							userSelect: 'none',
						}}>
						<div ref={this.mainRef}

							style={{
								transformOrigin: '0% 0%',
								transform: `scale(${ this.state.scale })`,
								position: 'relative',
							}}>
							<img onLoad={_ => {
								this.updateSize()
							}} style={{ width: '100%' }} alt='' src={this.state.page.src}
								onError={_ => {
									this.setState({ loadedImage: false })
								}}></img>
							{
								this.state.loadedImage === false ?
									<Empty></Empty> :
									<div style={{
										position: 'absolute', top: 0, left: 0, right: 0, bottom: 0,
									}}>
										<svg ref={this.svgRef} xmlns="http://www.w3.org/2000/svg" version="1.1"
											preserveAspectRatio="none"
											width='100%' height='100%'
											viewBox={`0 0 500 ${ this.state.size.height / this.state.size.sclar }`}>
										</svg>
									</div>
							}

							<div contentEditable id='svgmain_input' style={{
								position: 'absolute', fontSize: '2em',
								border: '1px dashed red', outline: 'none',
								backgroundColor: 'rgba(255,255,255,0.5)', top: 0, color: 'red',
								visibility: 'hidden', minWidth: 96, textAlign: 'left'
							}} onMouseDown={e => {
								e.stopPropagation()
							}}>
							</div>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

//const styles: { [key: string]: React.CSSProperties } = {
//}

export default A;