import React from 'react';
import { Form, Button, Input, Switch } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import moment from 'moment';
export const formItemLayout = {
	labelCol: {
		xs: { span: 24 },
		sm: { span: 8 },
	},
	wrapperCol: {
		xs: { span: 24 },
		sm: { span: 12 },
	},
};
export const tailFormItemLayout = {
	wrapperCol: {
		xs: {
			span: 24,
			offset: 0,
		},
		sm: {
			span: 12,
			offset: 8,
		},
	},
};

interface Props extends FormComponentProps {
	submit: (data: any) => void
}

export const PaperForm: React.FC<Props> = (props) => {

	const handleSubmit = () => {
		props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
				props.submit({ ...values, paperId: 0, psNameRule: '', isStop: values.isStop ? 1 : 0 })
				props.form.resetFields()
			}
		});
	}

	const getFieldDecorator = props.form.getFieldDecorator

	return (
		<Form {...formItemLayout} onSubmit={handleSubmit}>

			<Form.Item label="报纸名称">
				{getFieldDecorator('name', {
					rules: [{ required: true, type: 'string', message: '请填写报纸名称' }]
				})(<Input />)}
			</Form.Item>

			<Form.Item label="报纸代码">
				{getFieldDecorator('code', {
					rules: [{ required: true, type: 'string', message: '请填写报纸代码' }]
				})(<Input />)}
			</Form.Item>

			<Form.Item label='创建日期'>
				{getFieldDecorator('createDate', {
					rules: [{ required: true, type: 'string', message: '请填写创建日期' }],
					initialValue: moment(Date.now()).format( "YYYY-MM-DD hh:mm:ss")
				})(<Input />)}</Form.Item>

			<Form.Item label='是否停发'>
				{
					getFieldDecorator('isStop', {
						rules: [{ required: true, type: 'boolean' }],
						valuePropName: 'checked',
						initialValue: false
					})(<Switch></Switch>)
				}
			</Form.Item>

			<Form.Item {...tailFormItemLayout}>
				<Button type="primary" htmlType="submit" style={{ minWidth: '120px' }}>提交</Button>
			</Form.Item>
		</Form>
	)
}

export default Form.create<Props>()(PaperForm);