import React, { useState, useEffect, useCallback } from 'react';
import { message, Icon, Checkbox, Layout, Menu, List } from 'antd';
import request from '../request';
import { RouteComponentProps } from 'react-router';

const { Sider, Content } = Layout;

const A: React.FC<RouteComponentProps> = (props) => {

	const [prgs, setPrgs] = useState<{ loginId: string, deptId: number }[]>([])
	const [dept, setDept] = useState<{ deptId: number, name: string, employees?: { loginId: string, userName: string }[] }[]>([])
	const [roleId] = useState(parseInt(new URLSearchParams(props.location.search).get('roleId') || '0') || 0)
	const [current, setCurrent] = useState<{ loginId: string, userName: string }[]>([])
	const [currentDept, setCurrentDept] = useState(-1)

	const [loading, setLoading] = useState(false)

	const updateRoleEmployee = useCallback(() => {

		request.emprole.getRoleEmployee({
			pageIndex: 1,
			pageSize: 100,
			roleId
		}).then(res => {

			if (res.status && res.data.status) {

				setPrgs(res.data.data.rows)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [roleId])


	const getEmployees = (deptId: number) => {
		setCurrentDept(deptId)

		let i = dept.findIndex(v => v.deptId === deptId)

		if (-1 === i || dept[i].employees !== undefined) {
			return
		}

		setLoading(true)
		request.employee.getDeptEmployee({
			pageIndex: 1,
			pageSize: 100,
			deptId
		}).then(res => {

			if (res.status && res.data.status) {

				dept[i].employees = res.data.data.rows

				setDept([...dept])
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}

			setTimeout(() => {
				setLoading(false)
			}, 300);
		})
	}


	const selectEmployee = (loginId: string, selected: boolean) => {
		if (selected) {
			setLoading(true)
			request.emprole.addRoleEmployee({ loginId, roleId }).then(res => {
				setTimeout(() => {
					setLoading(false)
				}, 500);

				if (res.status === 200 && res.data.status) {
					message.success('角色人员修改已保存！')
					updateRoleEmployee()
				} else {
					message.error((res.data && res.data.errorMsg) || res.statusText)
				}
			})
		} else {

			setLoading(true)
			request.emprole.deleteRoleEmployee({ loginId, roleId }).then(res => {
				setTimeout(() => {
					setLoading(false)
				}, 500);

				if (res.status === 200 && res.data.status) {
					message.success('角色人员修改已保存！')
					updateRoleEmployee()
				} else {
					message.error((res.data && res.data.errorMsg) || res.statusText)
				}
			})
		}
	}


	useEffect(() => {

		updateRoleEmployee()

		request.dept.getDeptInfo({
			pageIndex: 1,
			pageSize: 100,
			roleId
		}).then(res => {

			if (res.status && res.data.status) {
				setDept(res.data.data.rows)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [roleId, updateRoleEmployee])

	useEffect(() => {

		const item = dept.find(v => v.deptId === currentDept)
		if (item && item.employees) {
			const v1 = current.map(v => v.loginId).join()
			const v2 = item.employees.map(v => v.loginId).join()

			if (v1 !== v2) {
				setCurrent(item.employees)
			}
		}

	}, [current, currentDept, dept])

	return (
		<Layout style={{ backgroundColor: '#fff' }}>
			<Sider theme='light' style={{ height: 'calc(100vh - 150px)', overflow: 'scroll', borderRight: '1px solid #eee' }}>
				<Menu mode='inline' style={{ borderRightWidth: 0 }}>
					{
						dept.map(v => <Menu.Item key={v.deptId} onClick={_ => {
							getEmployees(v.deptId)
						}}><Icon type="team" />{v.name}</Menu.Item>)
					}
				</Menu>
			</Sider>
			<Content>
				<List
					loading={loading}
					style={{ padding: 20 }}
					itemLayout="horizontal"
					dataSource={current}
					header='人员'
					renderItem={item =>
						<List.Item >
							<List.Item.Meta
								avatar={
									<Checkbox disabled={loading}
										checked={prgs.find(v => v.loginId === item.loginId) !== undefined}
										key={item.loginId} onChange={e => {
											selectEmployee(item.loginId, e.target.checked)
										}} style={{ marginLeft: 20 }}>
										启用角色
									</Checkbox>
								}
								title={<span><Icon type='user' /> {item.userName}</span>} />
							<div>{item.loginId}</div>
						</List.Item>}></List>
			</Content>
		</Layout>
	)
}

export default A;