import { svgNamespace, selectItem, unSelectItem, PainterInfo } from '../draw';
import { getDim } from '../../funcs';
import { lastPos } from './line';

export default class Rect {

	static newItem(painter: PainterInfo, sclar: number, e: React.MouseEvent<HTMLDivElement, MouseEvent>, svgRef: SVGElement) {

		unSelectItem(painter)
		let { x, y } = getDim(e.currentTarget);

		painter.move = 'rb';

		lastPos.x = (e.clientX - x) / sclar;
		lastPos.y = (e.clientY - y) / sclar
		lastPos.x0 = lastPos.x;
		lastPos.y0 = lastPos.y;

		const s1 = ((e.clientX - x) / sclar).toFixed(2);
		const s2 = ((e.clientY - y) / sclar).toFixed(2);

		const ele = document.createElementNS(svgNamespace, 'rect') as SVGRectElement;

		const cicle1 = document.createElementNS(svgNamespace, 'circle') as SVGCircleElement;
		const cicle2 = document.createElementNS(svgNamespace, 'circle') as SVGCircleElement;

		cicle1.setAttribute('cx', s1);
		cicle1.setAttribute('cy', s2);
		cicle1.setAttribute('r', '4');
		cicle1.setAttribute('stroke', painter.color);
		cicle1.setAttribute('stroke-width', '1');
		cicle1.setAttribute('fill', 'white');
		cicle1.setAttribute('pt', 'true')
		cicle1.setAttribute('stroke-dasharray', '1 1')

		cicle2.setAttribute('stroke-dasharray', '1 1')
		//方便删除
		cicle2.setAttribute('pt', 'true')
		cicle2.setAttribute('cx', s1);
		cicle2.setAttribute('cy', s2);
		cicle2.setAttribute('r', '4');
		cicle2.setAttribute('stroke', painter.color);
		cicle2.setAttribute('stroke-width', '1');
		cicle2.setAttribute('fill', 'white');

		ele.setAttribute('type', 'rect');
		ele.setAttribute('x', s1);
		ele.setAttribute('y', s2);
		ele.setAttribute('width', '0');
		ele.setAttribute('height', '0');
		ele.setAttribute('fill-opacity', '0');
		ele.setAttribute('stroke', painter.color);
		ele.setAttribute('stroke-width', painter.width);
		ele.setAttribute('stroke-linecap', 'round');
		ele.style.cursor = 'move';
		ele.onmousedown = e => {
			e.stopPropagation()

			painter.mouseDown = true
			unSelectItem(painter);
			lastPos.x0 = -1;
			lastPos.y0 = -1;
			painter.current = ele;
			painter.point1 = cicle1;
			painter.point2 = cicle2;
			painter.move = 'all';
			selectItem(painter);
		};

		cicle1.onmousedown = e => {
			e.stopPropagation()
			painter.mouseDown = true
			unSelectItem(painter);
			painter.current = ele;
			painter.point1 = cicle1;
			painter.point2 = cicle2;
			painter.move = 'lt';
			selectItem(painter);
		}
		cicle2.onmousedown = e => {
			e.stopPropagation()
			painter.mouseDown = true
			unSelectItem(painter);
			painter.current = ele;
			painter.point1 = cicle1;
			painter.point2 = cicle2;
			painter.move = 'rb';
			selectItem(painter);
		}

		ele.onmouseup = e => {

			e.stopPropagation()

			Rect.mouseup(painter, svgRef)
		}
		cicle1.onmouseup = e => {

			e.stopPropagation()

			Rect.mouseup(painter, svgRef)
		}
		cicle2.onmouseup = e => {

			e.stopPropagation()

			Rect.mouseup(painter, svgRef)
		}

		painter.current = ele;
		painter.point1 = cicle1;
		painter.point2 = cicle2;
		selectItem(painter);

		svgRef.appendChild(ele)
		svgRef.appendChild(cicle1)
		svgRef.appendChild(cicle2)
	}

	static mouseMove(painter: PainterInfo, sclar: number, e: React.MouseEvent<HTMLDivElement, MouseEvent>) {

		const { x, y } = getDim(e.currentTarget);

		if (-1 === lastPos.x0 && -1 === lastPos.y0) {
			lastPos.x0 = (e.clientX - x) / sclar
			lastPos.y0 = (e.clientY - y) / sclar
		}

		if (Math.abs(lastPos.x - lastPos.x0) < 4 && Math.abs(lastPos.y - lastPos.y0) < 4) {

			lastPos.x = (e.clientX - x) / sclar
			lastPos.y = (e.clientY - y) / sclar
			return
		}
		lastPos.x = (e.clientX - x) / sclar
		lastPos.y = (e.clientY - y) / sclar

		if (painter.move === 'rb') {

			if (painter.current) {

				const line = painter.current
				const x0 = parseFloat(line.getAttribute('x') || '0')
				const y0 = parseFloat(line.getAttribute('y') || '0')

				if ((e.clientX - x) / sclar <= x0 || (e.clientY - y) / sclar <= y0) {
					return
				}

				line.setAttribute('width', ((e.clientX - x) / sclar - x0) + '')
				line.setAttribute('height', ((e.clientY - y) / sclar - y0) + '')
			}
			if (painter.point2) {
				const line = painter.point2
				line.setAttribute('cx', (e.clientX - x) / sclar + '')
				line.setAttribute('cy', (e.clientY - y) / sclar + '')
			}
		}
		if (painter.move === 'lt') {

			if (painter.current) {

				const line = painter.current

				const x0 = parseFloat(line.getAttribute('x') || '0')
				const y0 = parseFloat(line.getAttribute('y') || '0')
				const w = parseFloat(line.getAttribute('width') || '0')
				const h = parseFloat(line.getAttribute('height') || '0')

				if (x0 + w <= (e.clientX - x) / sclar || y0 + h <= (e.clientY - y) / sclar) {
					return
				}

				line.setAttribute('x', (e.clientX - x) / sclar + '')
				line.setAttribute('y', (e.clientY - y) / sclar + '')

				line.setAttribute('width', (x0 + w - (e.clientX - x) / sclar) + '')
				line.setAttribute('height', (y0 + h - (e.clientY - y) / sclar) + '')
			}
			if (painter.point1) {
				const line = painter.point1
				line.setAttribute('cx', (e.clientX - x) / sclar + '')
				line.setAttribute('cy', (e.clientY - y) / sclar + '')
			}
		}
		if (painter.move === 'all') {

			if (painter.point1) {
				const line = painter.point1
				const cx = parseFloat(line.getAttribute('cx') || '0')
				const cy = parseFloat(line.getAttribute('cy') || '0')
				line.setAttribute('cx', (cx + lastPos.x - lastPos.x0) + '')
				line.setAttribute('cy', (cy + lastPos.y - lastPos.y0) + '')
			}
			if (painter.point2) {
				const line = painter.point2
				const cx = parseFloat(line.getAttribute('cx') || '0')
				const cy = parseFloat(line.getAttribute('cy') || '0')
				line.setAttribute('cx', (cx + lastPos.x - lastPos.x0) + '')
				line.setAttribute('cy', (cy + lastPos.y - lastPos.y0) + '')
			}
			if (painter.current) {
				const line = painter.current
				const x1 = parseFloat(line.getAttribute('x') || '0')
				const y1 = parseFloat(line.getAttribute('y') || '0')

				line.setAttribute('x', (x1 + lastPos.x - lastPos.x0) + '')
				line.setAttribute('y', (y1 + lastPos.y - lastPos.y0) + '')
			}

			lastPos.x0 = lastPos.x;
			lastPos.y0 = lastPos.y;
		}
	}
	static mouseup(painter: PainterInfo, svgRef: SVGElement) {
		painter.mouseDown = false

		if (painter.current && painter.point1 && painter.point2) {
			const h = parseFloat(painter.current.getAttribute('width') || '0')
			const w = parseFloat(painter.current.getAttribute('height') || '0')

			if (w * w + h * h < 32) {
				svgRef.removeChild(painter.current)
				svgRef.removeChild(painter.point1)
				svgRef.removeChild(painter.point2)
				painter.current = undefined;
				painter.point1 = undefined;
				painter.point2 = undefined;
			} else {
				painter.changed = true
			}
		}
	}
}