import React, { useState, useEffect, useCallback } from 'react';
import { Table, Button, Modal, Typography, message, Popconfirm } from 'antd';
import PForm from './forms/role';
import request from '../request';
import { RouteChildrenProps } from 'react-router';
import { history } from '../index'
import { Link } from 'react-router-dom';
import store from '../store';

const A: React.FC<RouteChildrenProps> = (props) => {

	const [data, setData] = useState({
		pageIndex: 1,
		pageSize: 10,
		total: 0,
		rows: []
	})
	const [showForm, setShowForm] = useState(false)

	const columns: any[] = [
		{
			title: '角色名称', dataIndex: 'name', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					if (v.trim().length === 0) {
						message.error('角色名不能为空！')
						return
					}

					saveData({ ...item, name: v })
				}
			}}>{text}</Typography.Paragraph>
		},
		{
			title: '人员', key: 'roleId1', dataIndex: 'roleId', width: 120, align: 'center',
			render: (text: string, v: any) => <Link to={`/roleEmployee?roleId=${ text }`} onClick={_ => {

				const path = history.location.pathname + history.location.search

				store.dispatch({ type: 'fold', data: { name: v.name, path: path } })

			}}>查看人员</Link>
		},
		{
			title: '权限', key: 'roleId2', dataIndex: 'roleId', width: 120, align: 'center',
			render: (text: string, v: any) => <Link to={`/roleRight?roleId=${ text }`} onClick={_ => {

				const path = history.location.pathname + history.location.search

				store.dispatch({ type: 'fold', data: { name: v.name, path: path } })

			}}>查看权限</Link>
		},
		{
			title: '备注', dataIndex: 'remark', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					saveData({ ...item, remark: v })
				}
			}}>{text || ''}</Typography.Paragraph>
		},
		{
			title: '编辑', key: 'action2', width: 80, align: 'center',
			render: (v: any) => <Popconfirm
				title="确定要删除该角色吗?"
				onConfirm={_ => {
					request.role.deleteRoleInfo(v).then(res => {
						if (res.status && res.data.status) {
							message.success('角色已删除！')
							updateData()
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
					})
				}}>
				<Button type='danger' >删除</Button>
			</Popconfirm>

		},
	];

	/**
	 * 保存修改项
	 * @param item 
	 */
	const saveData = (item: any) => {
		request.role.updateRoleInfo(item).then(res => {
			if (res.status && res.data.status) {
				message.success('角色信息已保存！')
				updateData()
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}

	/**
	 * 更新列表数据
	 */
	const updateData = useCallback(() => {
		const s = new URLSearchParams(props.location.search)

		const search = {
			pageIndex: parseInt(s.get('pageIndex') || '1'),
			pageSize: parseInt(s.get('pageSize') || '10'),
			roleType: 0
		}
		request.role.getRoleInfo(search).then(res => {

			if (res.status && res.data.status) {
				setData(res.data.data)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [props.location.search])

	useEffect(() => {
		updateData()

	}, [updateData])

	return (
		<div>
			<div style={{ padding: '12px 16px', borderBottom: '6px solid #eee' }}>
				<Button type='primary' icon='plus' onClick={_ => setShowForm(true)}>添加角色</Button>
			</div>
			<Table dataSource={data.rows.map((v: any) => ({ ...v, key: v.roleId }))}
				columns={columns}
				scroll={{ x: 960 }}
				pagination={{
					showSizeChanger: true,
					current: data.pageIndex,
					total: data.total,
					pageSize: data.pageSize,
					pageSizeOptions: ['10', '20', '40']
				}}
				onChange={v => {
					const s = new URLSearchParams(props.location.search)
					if (v.pageSize) {
						s.set('pageSize', v.pageSize + '')
					}
					if (v.current) {
						s.set('pageIndex', v.current + '')
					}

					history.push(props.location.pathname + '?' + s.toString())

				}} />
			<Modal
				title='新建角色'
				visible={showForm}
				closable
				maskClosable={false}
				footer={null}
				onCancel={_ => setShowForm(false)}>
				<PForm
					submit={(v) => {
						request.role.newRoleInfo(v).then(res => {

							if (res.status && res.data.status) {
								message.success('角色添加完成！')
								updateData()
							} else {
								message.error((res.data && res.data.errorMsg) || res.statusText)
							}
							setShowForm(false)
						})

					}}></PForm>
			</Modal>
		</div>
	)
}

export default A;