import React from 'react';
import { Form, Button, Input } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { formItemLayout, tailFormItemLayout } from './paper';

interface Props extends FormComponentProps {
	submit: (data: any) => void
}

export const PaperForm: React.FC<Props> = (props) => {

	const handleSubmit = () => {
		props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
				props.submit({ ...values, deptId: 0, psNameRule: '', isStop: values.isStop ? 1 : 0 })
				props.form.resetFields()
			}
		});
	}

	const getFieldDecorator = props.form.getFieldDecorator

	return (
		<Form {...formItemLayout} onSubmit={handleSubmit}>

			<Form.Item label="部门名称">
				{getFieldDecorator('name', {
					rules: [{ required: true, type: 'string', message: '请填写部门名称' }]
				})(<Input />)}
			</Form.Item>
			<Form.Item label="部门代码">
				{getFieldDecorator('code', {
					rules: [{ required: true, type: 'string', message: '请填写部门代码' }]
				})(<Input />)}
			</Form.Item>

			<Form.Item label="联系人">
				{getFieldDecorator('who')(<Input />)}
			</Form.Item>
			<Form.Item label="电话">
				{getFieldDecorator('phone')(<Input />)}
			</Form.Item>
			<Form.Item label="邮箱">
				{getFieldDecorator('email')(<Input />)}
			</Form.Item>
			<Form.Item label="传真">
				{getFieldDecorator('fax')(<Input />)}
			</Form.Item>

			<Form.Item {...tailFormItemLayout}>
				<Button type="primary" htmlType="submit" style={{ minWidth: '120px' }}>提交</Button>
			</Form.Item>
		</Form>
	)
}

export default Form.create<Props>()(PaperForm);