import React, { useState, useEffect, useCallback } from 'react';
import { Table, Button, Modal, Switch, Typography, message, Popconfirm } from 'antd';
import PForm from './forms/page';
import request from '../request';
import { RouteComponentProps } from 'react-router';
import { history } from '../index'

const A: React.FC<RouteComponentProps> = (props) => {

	const [data, setData] = useState({
		pageIndex: 1,
		pageSize: 10,
		total: 0,
		rows: []
	})
	const [showForm, setShowForm] = useState(false)

	const columns: any[] = [
		{
			title: '版面名称', dataIndex: 'name', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					if (v.trim().length === 0) {
						message.error('版面名不能为空！')
						return
					}

					saveData({ ...item, name: v })
				}
			}}>{text}</Typography.Paragraph>
		},

		{
			title: '版面代码', dataIndex: 'code', width: 160,
			render: (text: string, item: any) => <Typography.Paragraph editable={{
				onChange: (v) => {
					if (text === v) { return }
					if (v.trim().length === 0) {
						message.error('版面代码不能为空！')
						return
					}
					saveData({ ...item, code: v })
				}
			}}>{text}</Typography.Paragraph>
		},
		{
			title: '创建日期', dataIndex: 'createDate', width: 160, align: 'center',
		},
		{
			title: '是否停发', dataIndex: 'isStop', width: 160, align: 'center',
			render: (v: number, item: any) => <Switch defaultChecked={v === 1} onChange={v => {
				saveData({ ...item, isStop: v ? 1 : 0 })
			}}></Switch>
		},
		{
			title: '是否临时', dataIndex: 'isTemp', width: 160, align: 'center',
			render: (v: number, item: any) => <Switch defaultChecked={v === 1} onChange={v => {
				saveData({ ...item, isTemp: v ? 1 : 0 })
			}}></Switch>
		},
		{
			title: '编辑', key: 'action2', width: 80, align: 'center',
			render: (v: any) => <Popconfirm
				title="确定要删除该版面吗?"
				onConfirm={_ => {
					request.page.deletePaperPage(v).then(res => {
						if (res.status ===200 && res.data.status) {
							message.success('版面已删除！')
							updateData()
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}
					})
				}}>
				<Button type='danger' >删除</Button>
			</Popconfirm>
		},
	];

	/**
	 * 保存修改项
	 * @param item 
	 */
	const saveData = (item: any) => {
		request.page.updatePaperPage(item).then(res => {
			if (res.status ===200 && res.data.status) {
				message.success('叠次信息已保存！')
				updateData()
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}

	/**
	 * 更新列表数据
	 */
	const updateData = useCallback(() => {
		const s = new URLSearchParams(props.location.search)
		const search = {
			paperId: parseInt(s.get('paperId') || '0'),
			fold: s.get('fold') || '',
			pageIndex: parseInt(s.get('pageIndex') || '1'),
			pageSize: parseInt(s.get('pageSize') || '10')

		}
		request.page.getPaperPage(search).then(res => {

			if (res.status ===200 && res.data.status) {
				setData(res.data.data)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [props.location.search])

	useEffect(() => {
		updateData()
	}, [updateData])

	return (
		<div>
			<div style={{ padding: '12px 16px', borderBottom: '6px solid #eee' }}>
				<Button type='primary' icon='plus' onClick={_ => setShowForm(true)}>添加版面</Button>
			</div>
			<Table dataSource={data.rows.map((v: any) => ({ ...v, key: v.pageId }))}
				columns={columns}
				scroll={{ x: 640 }}
				pagination={{
					showSizeChanger: true,
					current: data.pageIndex,
					total: data.total,
					pageSize: data.pageSize,
					pageSizeOptions: ['10', '20', '40']
				}}
				onChange={v => {
					const s = new URLSearchParams(props.location.search)
					if (v.pageSize) {
						s.set('pageSize', v.pageSize + '')
					}
					if (v.current) {
						s.set('pageIndex', v.current + '')
					}

					history.push(props.location.pathname + '?' + s.toString())

				}} />
			<Modal
				title='新建版面'
				visible={showForm}
				closable
				maskClosable={false}
				footer={null}
				onCancel={_ => setShowForm(false)}>
				<PForm submit={(v) => {
					const s = new URLSearchParams(props.location.search)

					request.page.newPaperPage({
						...v,
						paperId: parseInt(s.get('paperId') || '0'),
						fold: s.get('fold') || ''
					}).then(res => {

						if (res.status && res.data.status) {
							updateData()
							message.success('版面创建完成。')
						} else {
							message.error((res.data && res.data.errorMsg) || res.statusText)
						}

						setShowForm(false);
					})

				}}></PForm>
			</Modal>
		</div>
	)
}

export default A;