import React from 'react';
import { Button, Icon, Tooltip } from 'antd';

import image1 from '../assets/juxing.png';
import image2 from '../assets/yuanxing.png';
import image3 from '../assets/jiantou.png';
import image4 from '../assets/huabi.png';
import image5 from '../assets/line.png';
import image6 from '../assets/wenzi.png';
import image7 from '../assets/zhuashou.png';

import image11 from '../assets/yuandian1.png';
import image12 from '../assets/yuandian2.png';
import image13 from '../assets/yuandian3.png';
import image14 from '../assets/yuandian4.png';
import { cancelFullScreen } from '../funcs';
import { connect } from 'react-redux';

export type ToolType = 'line' | 'rect' | 'ellipse' | 'pen' | 'text' | 'direct' | 'hand'

interface Props {
	currentTool: string
	currentSize: string
	currentColor: string
	setCurrentTool: (v: string, w: ToolType) => void
	setCurrentSize: (v: string, w: string) => void
	setColor: (v: string) => void
	onDelete: () => void
	fullscreen: boolean
	scale: (isLarger: boolean) => void
	refM: React.RefObject<HTMLDivElement>
	save: () => void
	disabled: boolean
	draw: boolean
	onFullscreen: () => void
	dispatch: (d: { type: string }) => void
	close?: () => void
}

export function getSize(w: string) {

	const item = items2.find(v => v.type === w)

	return (item && item.image) || 'image12'
}

export function getType(w: string) {

	const item = items1.find(v => v.type === w)
	return (item && item.image) || 'image1'
}

const items1: { image: string, type: ToolType, src: string, title: string }[] = [
	{ image: 'image7', type: 'hand', src: image7, title: '抓手工具' },
	{ image: 'image1', type: 'rect', src: image1, title: '矩形工具' },
	{ image: 'image2', type: 'ellipse', src: image2, title: '圆形工具' },
	{ image: 'image3', type: 'direct', src: image3, title: '箭头工具' },
	{ image: 'image4', type: 'pen', src: image4, title: '贝塞尔曲线工具' },
	{ image: 'image5', type: 'line', src: image5, title: '直线工具' },
	{ image: 'image6', type: 'text', src: image6, title: '文字工具' },
]

const items2: { image: string, type: string, src: string }[] = [
	{ image: 'image11', type: '1', src: image11 },
	{ image: 'image12', type: '2', src: image12 },
	{ image: 'image13', type: '4', src: image13 },
	{ image: 'image14', type: '6', src: image14 }
]

const items3: { image: string }[] = [
	{ image: 'red' },
	{ image: 'green' },
	{ image: 'blue' },
	{ image: 'black' },
	{ image: 'pink' },
	{ image: 'orange' },
	{ image: 'brown' },
	{ image: 'cyan' },
]

export const colorItems = items3;

const A: React.FC<Props> = (props) => {



	if (props.draw === false) {
		return (
			<div ref={props.refM} className='tools' style={{ opacity: props.disabled ? 0.5 : 1 }}>
				<div>

					<Button disabled={props.disabled} size='small' type='link' icon='zoom-out' onClick={_ => {
						props.scale(false)
					}}></Button>
					<Button disabled={props.disabled} size='small' type='link' icon='zoom-in' onClick={_ => {
						props.scale(true)
					}}></Button>
					<Button disabled={props.disabled} size='small' type='link' icon={props.fullscreen ? 'fullscreen-exit' : 'fullscreen'}
						onClick={_ => {
							if (props.fullscreen) {
								cancelFullScreen()

							} else {
								props.onFullscreen()
							}

						}}></Button>
					<Button disabled={props.disabled} size='small' type='link' icon='close' onClick={_ => {
						props.close && props.close()
					}}></Button>

				</div>

			</div>
		)
	}

	return (
		<div ref={props.refM} className='tools' style={{ opacity: props.disabled ? 0.5 : 1 }}>

			<div>
				{items1.map(v =>
					<Button disabled={props.disabled} key={v.image} size='small' title={v.title} type='link' onClick={_ => {
						props.setCurrentTool(v.image, v.type)
					}}><img src={v.src} alt=''
						className={props.currentTool === v.image ? 'selected' : ''}></img></Button>)
				}
				<Tooltip title='删除'>
					<Button disabled={props.disabled} size='small' type='link' title='撤回' icon='delete' onClick={_ => {
						props.onDelete()
					}}></Button>
				</Tooltip>
			</div>
			<div >
				{items2.map(v =>
					<Button disabled={props.disabled} key={v.image} size='small' type='link' onClick={_ => {
						props.setCurrentSize(v.image, v.type)
					}}><img src={v.src} alt=''
						className={props.currentSize === v.image ? 'selected' : ''}></img></Button>
				)}

			</div>
			<div>
				{items3.map(v =>
					<Button disabled={props.disabled} key={v.image} size='small' type='link' className={props.currentColor === v.image ? 'selected' : ''}
						onClick={_ => {
							props.setColor(v.image)
						}}>
						<Icon type='tablet' theme='filled' style={{ color: v.image }}></Icon>
					</Button>)
				}
			</div>
			<div>

				<Button disabled={props.disabled} size='small' type='link' icon='zoom-out' title='缩小' onClick={_ => {
					props.scale(false)
				}}></Button>
				<Button disabled={props.disabled} size='small' type='link' icon='zoom-in' title='放大' onClick={_ => {
					props.scale(true)
				}}></Button>
				<Button disabled={props.disabled} size='small' title='全屏' type='link' icon={props.fullscreen ? 'fullscreen-exit' : 'fullscreen'} onClick={_ => {
					if (props.fullscreen) {
						cancelFullScreen()
					} else {
						props.onFullscreen()
					}

				}}></Button>
			</div>
			<div>
				<Button disabled={props.disabled} size='small' type='primary' icon='cloud-upload' onClick={_ => {
					props.save()
				}} title='保存审版痕迹'></Button>
			</div>
		</div>
	)
}

export default connect<{ fullscreen: boolean }>((state: any) => ({ fullscreen: state.fullScreen }))(A);