import store from '../store';

export function dateFormat(date: Date, fmt: string) {
	var o: { [key: string]: any } = {
		"M+": date.getMonth() + 1,                 //月份 
		"d+": date.getDate(),                    //日 
		"h+": date.getHours(),                   //小时 
		"m+": date.getMinutes(),                 //分 
		"s+": date.getSeconds(),                 //秒 
		"q+": Math.floor((date.getMonth() + 3) / 3), //季度 
		"S": date.getMilliseconds()             //毫秒 
	};
	if (/(y+)/.test(fmt)) {
		fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
	}
	for (var k in o) {
		if (new RegExp("(" + k + ")").test(fmt)) {
			fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
		}
	}
	return fmt;
}

export function getDim(htmlObj: HTMLElement | null) {
	var rd = { x: 0, y: 0 }
	var ele = htmlObj

	while (ele) {

		rd.x += ele.offsetLeft
		rd.y += ele.offsetTop

		ele = ele.offsetParent && ele.offsetParent as HTMLElement

	}
	ele = htmlObj
	while (ele) {

		if (ele === document.body)
			break;

		rd.y -= ele.scrollTop
		rd.x -= ele.scrollLeft
		ele = ele.parentElement
	}

	return rd
}

export function requestFullScreen(videoDom: any) {
	if (videoDom.requestFullscreen) {

		return videoDom.requestFullscreen();

	} else if (videoDom.webkitRequestFullScreen) {

		return videoDom.webkitRequestFullScreen();

	} else if (videoDom.mozRequestFullScreen) {

		return videoDom.mozRequestFullScreen();

	} else {

		return videoDom.msRequestFullscreen();

	}

}

export function cancelFullScreen() {

	if (store.getState().fullScreen === false) {
		return
	}

	const e = document as any
	if (e.exitFullscreen) {
		e.exitFullscreen();
	} else if (e.mozExitFullScreen) {
		e.mozExitFullScreen();
	} else if (e.webkitExitFullscreen) {
		e.webkitExitFullscreen();
	}
}

export function onFullScreenEvent(callback: (s: boolean) => void) {

	document.onfullscreenchange = e => {

		callback(document.fullscreen);
	}
}

