import React, { useState, useCallback, useEffect, useRef } from 'react';
import { Layout, Icon, DatePicker, Menu, message, List, Modal } from 'antd';
import moment from 'moment';
import { PaperData } from './pages/roleRight';
import request from './request';
import Pages from './draw/pages';
import './draw/draw.css';
import Draw, { painter } from './draw/draw';
import Historys from './draw/historys'

const { Sider } = Layout;

export interface SelectedItem {
	pageVer: number,
	paperId: number,
	pageId: number,
	pfold: string,
	pageDate: string,
}

interface HistoryItem {
	userName: string,
	pageUrl: string,
	pageSvgInfo: string,
	disposeDateTime: number,
	pageVer: number
}

const A: React.FC = () => {

	const [showHistory, setShowHistory] = useState(false)
	const [openKeys, setOpenKeys] = useState<string[]>([])
	const [selectedKeys, setSelectedKeys] = useState<string[]>([])
	const [showModel, setShowModel] = useState(false)
	const [currentPage, setCurrentPage] = useState<{ paperId: number, pageId: number, pfold: string }>({
		paperId: -1,
		pageId: -1,
		pfold: ''
	})
	const [paperData, setPaperData] = useState<PaperData[]>([])
	const [selectedMenuItems, setSelectedMenuItems] = useState<(number | string)[]>([])
	const [historys, setHistorys] = useState<HistoryItem[]>([])
	const { SubMenu } = Menu;
	const [selectItem, setSelectItem] = useState<SelectedItem>({
		pageVer: 0,
		paperId: -1,
		pageId: -1,
		pfold: '',
		pageDate: moment(new Date()).format('YYYY/MM/DD'),
	})
	const [menuIndex, setMenuIndex] = useState(-1)
	const refPage = useRef<Pages>(null)
	const refDraw = useRef<Draw>(null)
	const refHistory = useRef<Historys>(null)

	/**
	 * 获取报纸
	 */
	const getPaper = useCallback((fouce: boolean = false) => {

		if (!fouce && paperData.length > 0) {
			return
		}

		request.paper.getPaperInfo({
			pageIndex: 1,
			pageSize: 100
		}).then(res => {

			if (res.status && res.data.status) {
				setPaperData(res.data.data.rows)
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [paperData.length])
	/**
		 * 获取叠次
		 * @param paperId 报纸ID
		 */
	const getFold = useCallback((paperId: number) => {

		let i = paperData.findIndex(v => v.paperId === paperId)

		if (-1 === i || paperData[i].folds !== undefined) {
			return
		}

		request.fold.getPaperFold({
			pageIndex: 1,
			pageSize: 100,
			paperId
		}).then(res => {

			if (res.status && res.data.status) {

				paperData[i].folds = res.data.data.rows

				setPaperData([...paperData])
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [paperData])

	/**
	 * 获取版面
	 * @param paperId 报纸ID
	 * @param fold 叠次ID
	 */
	const getPage = useCallback((paperId: number, fold: string) => {
		const i = paperData.findIndex(v => v.paperId === paperId)

		if (-1 === i || paperData[i].folds === undefined) {
			return
		}

		const j = paperData[i].folds!.findIndex(sv => sv.fold === fold)

		if (-1 === j || paperData[i].folds![j].papers !== undefined) {
			return
		}

		request.page.getPaperPage({
			pageIndex: 1,
			pageSize: 100,
			paperId,
			fold
		}).then(res => {

			if (res.status === 200 && res.data.status) {

				paperData[i].folds![j].papers = res.data.data.rows
				setPaperData([...paperData])
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}, [paperData])

	const selectPaper = (item: any, name: any) => {

		if (selectItem.paperId === item && selectItem.pfold === '') {
			refPage.current && refPage.current.show()
			return
		}

		setSelectedMenuItems([item])
		setSelectItem({ ...selectItem, paperId: item, pfold: '', pageId: -1 })

		refPage.current && refPage.current.showNew({ ...selectItem, paperId: item, pfold: '', pageId: -1 }, [name])
	}
	const selectFold = (item: any, item1: any, name: any) => {

		if (selectItem.paperId === item && selectItem.pfold === item1) {
			refPage.current && refPage.current.show()
			return
		}

		setSelectedMenuItems([item, item1])
		setSelectItem({ ...selectItem, paperId: item, pfold: item1, pageId: -1 })

		refPage.current && refPage.current.showNew({ ...selectItem, paperId: item, pfold: item1, pageId: -1 }, [name, item1])
	}

	const selectPage = (item: any) => {

		if (painter.changed) {
			setCurrentPage(item)
			setShowModel(true)
		} else {

			openPage(item)
		}
	}

	const openPage = (item: any) => {

		setSelectedKeys(['3_' + item.pageId + item.pfold + item.paperId])
		setShowHistory(true)
		setHistorys([])
		setSelectItem({ ...selectItem, ...item })
		refPage.current && refPage.current.hide()
		refDraw.current && refDraw.current.getPageReview({ ...item, pageDate: selectItem.pageDate })

		request.page.getPageImageReview({ ...item, pageDate: selectItem.pageDate }).then(res => {

			if (res.status && res.data.status) {
				setHistorys(res.data.data.rows.reverse())
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}

	const updateHistory = () => {

		request.page.getPageImageReview(selectItem).then(res => {

			if (res.status && res.data.status) {

				setHistorys(res.data.data.rows.reverse())
			} else {
				message.error((res.data && res.data.errorMsg) || res.statusText)
			}
		})
	}

	useEffect(() => {
		getPaper()
	}, [getPaper])

	return (
		<Layout style={{ height: '100vh' }}>
			<Sider
				theme='light' collapsible collapsedWidth={0} width={240}
				onCollapse={e => {
					setTimeout(() => {
						refDraw.current && refDraw.current.updateSize()
					}, 500);
				}}
				style={{ height: '100vh', borderRight: '1px solid #eee' }}>

				<div style={{
					width: 240,
					textAlign: 'center',
					fontWeight: 'bold',
					fontSize: '2em',
					display: 'inline-block',
					color: '#369',
					lineHeight: '60px',

					overflow: 'hidden'
				}}><Icon type="audit" />大样审版</div>

				<div style={{ height: 32, border: '4px solid #fff', backgroundColor: '#fff' }}>
					<DatePicker popupStyle={{ zIndex: 999999 }} onChange={(v, s) => {
						setSelectItem({ ...selectItem, pageDate: s })
						refPage.current && refPage.current.showNew({ ...selectItem, pageDate: s })
					}} value={moment(selectItem.pageDate)} format='YYYY/MM/DD'
						style={{ width: 220, marginLeft: 10 }} />
				</div>


				<Menu mode="inline" style={{
					borderRight: 'none',
					height: showHistory && historys.length > 0 ? 'calc(60vh - 100px)' : 'calc(100vh - 100px)',
					overflowY: 'scroll'
				}} openKeys={openKeys} selectedKeys={selectedKeys}>

					{paperData.map(v =>
						<SubMenu key={'1_' + v.paperId} title={<span
							className={selectedMenuItems.length === 1 &&
								selectedMenuItems[0] === v.paperId ? 'role-menu-selected' : ''}
							onClick={e => {
								e.stopPropagation()
								selectPaper(v.paperId, v.name)
							}}><Icon type="read" /><span>{v.name}</span></span>}
							onTitleClick={e => {
								if (openKeys.includes(e.key)) {
									setOpenKeys(openKeys.filter(v => v !== e.key))
								} else {
									setOpenKeys([...openKeys, e.key])
								}
								getFold(v.paperId)
							}}>

							{v.folds && v.folds.map(sv =>
								<SubMenu key={'2_' + sv.fold + v.paperId}
									title={<>
										<span
											className={selectedMenuItems.length === 2 &&
												selectedMenuItems[0] === v.paperId &&
												selectedMenuItems[1] === sv.fold ? 'role-menu-selected' : ''}
											onClick={e => {
												e.stopPropagation()
												selectFold(v.paperId, sv.fold, v.name)

											}}><Icon type="apartment" /><span >{sv.fold}</span></span>

									</>}
									onTitleClick={e => {
										if (openKeys.includes(e.key)) {
											setOpenKeys(openKeys.filter(v => v !== e.key))
										} else {
											setOpenKeys([...openKeys, e.key])
										}
										getPage(v.paperId, sv.fold)
									}}>

									{sv.papers && sv.papers.map((ssv: any) =>
										<Menu.Item key={'3_' + ssv.pageId + sv.fold + v.paperId}
											onClick={e => {
												selectPage({
													paperId: v.paperId,
													pfold: sv.fold,
													pageId: ssv.pageId
												})
											}}>
											<span><Icon type="file" />{ssv.name}</span>
										</Menu.Item>)
									}
									{
										(!sv.papers || sv.papers.length === 0) &&
										<Menu.Item key="1" disabled>没有找到版面！<Icon type='search' /></Menu.Item>
									}
								</SubMenu>)
							}
							{
								(!v.folds || v.folds.length === 0) &&
								<Menu.Item key="1" disabled>没有找到叠次！<Icon type='search' /></Menu.Item>
							}
						</SubMenu>)}

				</Menu>

				<div style={{ height: showHistory && historys.length > 0 ? '40vh' : '0', overflowY: 'scroll' }}>
					<div style={{ padding: '4px 12px', backgroundColor: '#fafafa', borderTop: '1px solid #eee' }}>历史版面</div>
					<List
						dataSource={historys}
						renderItem={(item, index) =>
							<List.Item key={index} onClick={e => {

								if (refHistory.current) {
									if (refHistory.current.show(item)) {
										setMenuIndex(index)
									} else {
										setMenuIndex(-1)
									}
								}

							}} style={index === menuIndex ?
								{ backgroundColor: '#e6f7ff', padding: 6, borderRight: '3px solid #1890ff' } :
								{ padding: 6 }}>
								<List.Item.Meta
									avatar={<div style={{
										width: 32, height: 32, borderRadius: 16,
										fontSize: '13px', lineHeight: '32px',
										backgroundColor: '#28a0ff', color: '#fff',
										textAlign: 'center'
									}}>{item.pageVer}</div>}
									title={new Date(item.disposeDateTime).toLocaleString()}
									description={<span><Icon type="user" /> {item.userName}</span>}
								/>
							</List.Item>
						}>

					</List>
				</div>
			</Sider>
			<Layout style={{ position: 'relative', height: '100vh', overflow: 'hidden' }}>
				<Draw ref={refDraw} updated={updateHistory}></Draw>
				<Historys ref={refHistory}></Historys>
				<Pages ref={refPage} select={(item) => {
					selectPage(item)
				}}></Pages>
				<Modal
					title="数据未保存"
					visible={showModel}
					onOk={_ => {

						openPage(currentPage)
						setShowModel(false)
					}}
					onCancel={_ => {
						setShowModel(false)
					}}
					okText="确认"
					cancelText="取消"
				>
					<h3>审版数据未上传，确定要切换版面吗？</h3>
					<p>点击确定，将打开新版面，审版数据不保存。</p>
					<p>点击取消，返回继续。</p>
				</Modal>
			</Layout>
		</Layout>
	)
}


export default A;